import React,{Component} from 'react'
import { Form, Icon, Input, Button } from 'antd'
import {getUserToken, getUserInfo} from "@/utils/api";
import * as constants from '@/store/constants'
import { connect } from 'react-redux';
import './style.scss'


const FormItem = Form.Item
class NormalLoginForm  extends Component {
  constructor (props) {
    super(props)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        getUserToken({acc: values.userName,pwd: values.password}).then((r) => {
          const res = r? r: {}
          if (res.status === 200) {
            this.props.handleChange()
            sessionStorage.Token = res.data.access_token
            // 保存用户id
            this.queryUserInfo(res.data.access_token)
            this.props.history.replace('/home/project')
          }
        })
      }
    });
  }
  // 获取用户信息
  queryUserInfo(token) {
    getUserInfo({token}).then(res => {
      sessionStorage.userId = res.data.principal.id
    })
  }
  render () {
    const { getFieldDecorator } = this.props.form;
    return (
      <div id={'login-box'}>

        <div className={'card'}>
          <h3>欢迎进入可视化后台管理系统</h3>
          <Form onSubmit={this.handleSubmit} className="login-form">
            <FormItem>
              {getFieldDecorator('userName', {
                rules: [
                  { required: true, message: '请输入易电e销手机号码!' }
                ]
              })(
                <Input
                  prefix={<Icon type="user"
                  style={{ color: 'rgba(0,0,0,.25)' }} />}
                  maxLength={11}
                  autoComplete={'off'}
                  placeholder={'请输入易电e销手机号码'}/>
              )}
            </FormItem>
            <FormItem>
              {getFieldDecorator('password', {
                rules: [
                  { required: true, message: '请输入易电e销账户密码!' },
                  { min: 6, message: '请输入6位以上密码' }
                ],
              })(
                <Input
                  prefix={<Icon type="lock"
                  style={{ color: 'rgba(0,0,0,.25)' }} />}
                  type="password"
                  placeholder={'请输入易电e销账户密码'}/>
              )}
            </FormItem>
            <FormItem>
              <Button type="primary" htmlType="submit" className="login-form-button">
                登录
              </Button>
            </FormItem>
          </Form>
        </div>
      </div>
    )
  }
}
NormalLoginForm = Form.create()(NormalLoginForm)

const mapDispatch = (dispatch) => ({
  handleChange() {
    const action = {
        type: constants.saveToken_ACTION,
        value: true,
    }
    dispatch(action)
  }
});
export default connect(null, mapDispatch)(NormalLoginForm);
