import React from 'react'
import { Row, Col} from 'antd';
import selectedImg from '@/assets/img/selected.png'
import './projectInfo.scss'

const ProjectInfo = (props) => {
  const { projectDetail, contactCategory } = props
  return (
    <div className="detail_content_box">
        <p className="detail_content_title">项目资料</p>
          <ul className="detail_content_item">
            <li>报备日期：{projectDetail.createdTime}</li>
            <li>所在区域：{projectDetail.country}</li>
            <li>项目阶段：{projectDetail.phase}</li>
            <li>业主单位名称：{projectDetail.proprietor}</li>
            <li>设计院名称：{projectDetail.designInstitute}</li>
            <li>盘厂：{projectDetail.completePlant ? projectDetail.completePlant : '--'}</li>
            <li>总包：{projectDetail.generalContractor ? projectDetail.generalContractor : '--'}</li>
            <li>投资方：{projectDetail.investor}</li>
            <li>预计招标时间：{projectDetail.estimatedTenderTime}</li>
            <li>预计合同签署时间：{projectDetail.estimatedContractTime}</li>
            <li>预计供货时间：{projectDetail.estimatedSupplyTime}</li>
          </ul>
          <p className="detail_content_title">客户需求说明</p>
          <ul className="detail_content_item">
            <li>产品品牌定位：{projectDetail.productOrientation}</li>
            <li>产品类型：{projectDetail.productCategory}</li>
            <li>预计产品数量：{projectDetail.productQuantity}</li>
          </ul>
          <p className="detail_content_title">我拥有的关键人脉</p>
          <Row gutter={16} className="resource-category detail_content_item">
            {
              contactCategory.map((v, index) => (
                <Col span={8} key={index}>
                  <div className='resource_item_box'>
                    {
                      v.isSelect ? <img alt="" src={selectedImg} className="selected"/> : <label className="not-select"></label>
                    }
                    <label htmlFor="" className="resource_item">{v.title}</label>
                  </div>
                </Col>
              ))
            }
          </Row>
          <p className="detail_content_title">我的联系方式</p>
          <ul className="detail_content_item">
            <li>称呼：{projectDetail.contact}</li>
            <li>手机：{projectDetail.contactTel}</li>
            <li>常驻地：{projectDetail.contactProvince?`${projectDetail.contactProvince}${projectDetail.contactCity}${projectDetail.contactDistrict}`:''}</li>
            <li>电子邮箱：{projectDetail.contactEmail === ''?'无':projectDetail.contactEmail}</li>
          </ul>
      </div>
  )
}

export default ProjectInfo
