import './style.scss'
import React, { Component } from 'react';
import moment from 'moment'
import {Button, Form, message, Radio, Input, DatePicker, Tag, Card} from 'antd'
import {getTaskPostponeList, putTaskStatus, openLoading} from "../../../utils/api";
const RadioGroup = Radio.Group
const FormItem = Form.Item
class Risk extends Component {
  constructor (props) {
    super(props)
    this.state = {
      radioValue: 0,
      inputValue: '',
      userlist: []
    }
    this.projectId = this.props.match.params.projectId
    this.taskId = this.props.match.params.taskId
    this.getUserList = this.getUserList.bind(this)
    this.radioChange = this.radioChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.inputChange = this.inputChange.bind(this)
  }
  componentDidMount() {
    openLoading()
    this.getUserList()
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (this.state.radioValue) {
          this.postPone('AGREED', values.deadline)
        } else {
          if (!this.state.inputValue) {
            message.error('请简短附言')
            return
          }
          this.postPone('DISAGREED', values.deadline)
        }
      }
    });
  }

  postPone (status, date) {
    let obj = {
      status,
      remark: this.state.inputValue
    }
    if (date) obj.deadline = moment(date, 'YYYY-MM-DD HH:mm:ss').valueOf()
    console.log(obj)
    putTaskStatus(this.projectId, this.taskId, obj).then((r) => {
      const res = r? r: {}
      if (res.status === 200) {
        message.success('处理成功')
        this.props.history.go(-1)
      }
    })
  }

  getUserList () {
    getTaskPostponeList(this.projectId, this.taskId).then((res) => {
      this.setState(() => ({
        userlist: res.data
      }))
    })
  }

  radioChange(e) {
    const value =  e.target.value
    this.setState(() => ({
      radioValue: value
    }))
  }

  inputChange(e) {
    const val = e.target.value
    this.setState(() => ({
      inputValue: val
    }))
  }

  render() {
    const {getFieldDecorator} = this.props.form
    const obj = {
      SERVANT: '服务者',
      COOPERATOR: '合作者'
    }
    return (
      <div id={'delay-box'}>
        <h4>申请人员列表</h4>
        <div className={'risk-userlist'}>
          {
            this.state.userlist.map((v) => {
              return(
                <Card key={v.name}>
                  <div className={'p'}><span>延期申请人：</span> <span className={'content'}>{v.name}<Tag color='#108ee9' style={{marginLeft: '10px'}}>{obj[v.role]}</Tag></span></div>
                  <div className={'p'}><span>申 请 时 间：</span> <span className={'content'}>{v.applyTime}</span></div>
                  <div className={'p'}><span>延 期 理 由：</span> <span className={'content'}>{v.reason}</span></div>
                </Card>
              )
            })
          }
        </div>
        <div style={{marginTop: '30px'}}>
          <RadioGroup onChange={this.radioChange} value={this.state.radioValue}>
            <Radio value={1}>同意</Radio>
            <Radio value={0}>不同意</Radio>
          </RadioGroup>
        </div>
        <Form onSubmit={this.handleSubmit}>
          {
            (() => {
              if (this.state.radioValue) {
                return(
                  <FormItem style={{marginTop: '30px'}}>
                    {
                      getFieldDecorator('deadline',{
                        rules: [{ required: true, message: '请选择要提醒的对象!' }]
                      })(
                        <DatePicker
                          showTime
                          format="YYYY-MM-DD HH:mm:ss"
                          placeholder="请选择延期截止时间"
                        />
                      )
                    }
                  </FormItem>
                )
              }
            })()
          }

          <FormItem style={{marginTop: '30px'}}>
            <Input.TextArea placeholder={'请简短附言'} onChange={this.inputChange} value={this.state.inputValue}/>
          </FormItem>

          <FormItem>
            <Button htmlType="submit">提交</Button>
          </FormItem>
        </Form>
      </div>
    )
  }
}
export default Form.create()(Risk);
