import React, { Component, Fragment } from 'react';
import {getDefaultNode, deleteDefaultNode, getProgramOfBase, postNodeAndProgram} from "../../../utils/api";
import './style.scss';
import {Table, Button, Popconfirm, Modal, message, Icon} from 'antd'
import AddForm from './addForm'
import EditForm from './editForm'
class node extends Component {
  constructor(props) {
    super(props)
    this.programColumns = [{
      title: '关键字',
      dataIndex: 'keywords',
      key: 'keywords',
      align: 'center'
    }]
    this.nodeColumns = [{
      title: '排序',
      dataIndex: 'orderNumber',
      key: 'orderNumber',
      align: 'center'
    }, {
      title: '节点名称',
      dataIndex: 'name',
      key: 'name',
      align: 'center'
    }, {
      title: '关联阶段数量',
      dataIndex: 'phaseCount',
      key: 'phaseCount',
      align: 'center'
    }, {
      title: '操作',
      align: 'center',
      key: 'action',
      render: (record) => (
        <Fragment>
          <Button onClick={this.nodeEdit.bind(this, record)}>编辑</Button>
          <Button onClick={this.AssociateProgram.bind(this, record)}>关联节方案</Button>
          <Popconfirm title="确认删除？" okText="确认" cancelText="取消" onConfirm={this.deleteNode.bind(this,record)}>
            <Button>删除</Button>
          </Popconfirm>
        </Fragment>
      ),
    }]
    this.state = {
      loading: false,
      visible: false,
      isEdit: false,
      nodeDataSource: [],
      total: 0,
      pTotal: 0,
      selectedRowKeys: [],
      visibleModal: false,
      programData: [],
      nodeData: {}
    }
    this.currentPage = 0
    this.handleCancel = this.handleCancel.bind(this)
    this.programConfirm = this.programConfirm.bind(this)
    this.addNode = this.addNode.bind(this)
    this.getDefaultNodeFuc = this.getDefaultNodeFuc.bind(this)
    this.pageChange = this.pageChange.bind(this)
    this.programPageChange = this.programPageChange.bind(this)
    this.getProgramOfBaseFuc = this.getProgramOfBaseFuc.bind(this)
  }
  componentDidMount() {
    this.getDefaultNodeFuc(this.currentPage)
  }

  getDefaultNodeFuc (page = 0) {
    const obj = {
      paging: true,
      page,
      size: 10
    }
    getDefaultNode(obj).then((res) => {
      const datas = res.data
      this.setState(() => ({
        nodeDataSource: datas.content,
        total: datas.totalElements
      }))
    })
  }

  deleteNode (row) {
    deleteDefaultNode(row.id).then((res) => {
      message.success('删除成功')
      this.getDefaultNodeFuc(this.currentPage)
    })
  }

  addNode () {
    this.setState(() => ({
      isEdit: false,
      visible: true
    }))
  }

  nodeEdit (record) {
    this.setState(() => ({
      isEdit: true,
      visible: true,
      nodeData: record
    }))
  }

  handleCancel () {
    this.setState(() => ({visible: false, visibleModal: false}))
  }

  AssociateProgram (row) {
    this.setState(() => ({loading: true, nodeData: row}))
    this.getProgramOfBaseFuc()
    this.setState(() => ({visibleModal: true}))
  }

  getProgramOfBaseFuc (page = 0) {
    const obj = {
      paging: true,
      page,
      size: 10
    }
    getProgramOfBase(obj).then((res) => {
      this.setState(() => ({loading: false}))
      const datas = res.data.content
      datas.forEach((v) => {
        v.keywords = v.keywords.join(',')
      })
      this.setState(() => ({programData: datas, pTotal: res.data.totalElements}))
    })
  }

  programConfirm () {
    const {selectedRowKeys, nodeData} = this.state
    this.handleCancel()
    postNodeAndProgram(nodeData.id, {solutions: selectedRowKeys}).then(() => {
      message.success('关联成功')
      this.getDefaultNodeFuc(this.currentPage)
    })
  }

  pageChange (page) {
    this.currentPage = page - 1
    this.getDefaultNodeFuc(page - 1)
  }

  programPageChange (page) {
    this.getProgramOfBaseFuc(page - 1)
  }

  editJsx () {
    if (this.state.isEdit) {
      return(<EditForm handleCancel={this.handleCancel} getDefaultNodeFuc={this.getDefaultNodeFuc} nodeData={this.state.nodeData}/>)
    } else {
      return(<AddForm handleCancel={this.handleCancel} getDefaultNodeFuc={this.getDefaultNodeFuc}/>)
    }
  }

  render() {
    const nodeRowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState(() => ({
          selectedRowKeys
        }))
      },
      selectedRowKeys: this.state.selectedRowKeys
    }
    return (
      <div id={'node-box'}>
        <div className={'title'}>
          节点预设 <Button onClick={this.addNode}> <Icon type="plus" />添加节点</Button>
        </div>
        <Modal
          title={this.state.isEdit?'编辑': '添加'}
          visible={this.state.visible}
          onCancel={this.handleCancel}
          footer={null}
        >
          {
            this.editJsx()
          }
        </Modal>

        <Modal
          title="关联方案"
          visible={this.state.visibleModal}
          onCancel={this.handleCancel}
          onOk={this.programConfirm}
          width={800}>
          <Table
            columns={this.programColumns}
            dataSource={this.state.programData}
            rowKey={key => key.id}
            pagination={{
              total: this.state.pTotal,
              onChange: this.programPageChange
            }}
            loading={this.state.loading}
            rowSelection={nodeRowSelection}
          />
        </Modal>

        <div className={'table-content'}>
          <Table
            columns={this.nodeColumns}
            dataSource={this.state.nodeDataSource}
            rowKey={key => key.id}
            pagination={{
              total: this.state.total,
              showQuickJumper: true,
              onChange: this.pageChange
            }}
            loading={this.state.loading}
          />
        </div>
      </div>
    )
  }
}
export default node
