import React, { Component } from 'react'
import { Tree, Modal, message } from 'antd';
import { getProjectPhases, postProjectPhases } from '@/utils/api'
import './stagePreset.scss'

const TreeNode = Tree.TreeNode;

class StagePreset extends Component {
  constructor(props) {
    super(props)
    this.state = {
      visible: false,
      projectNode: [],
      selectNode: [],
      defaultExpandedKeys: [], // 默认展开
      defaultCheckedKeys: [], // 默认选中项
    }
    this.selectedId = []
    this.projectId = this.props.projectId
    this.handleCancel = this.handleCancel.bind(this)
    this.handleConfirm = this.handleConfirm.bind(this)
    this.onCheck = this.onCheck.bind(this)
  }
  componentDidMount() {
    this.queryStage()
  }
  // 获取项目阶段
  queryStage () {
    getProjectPhases(this.projectId).then((res) => {
      let data = res.data.phases
      let defaultExpandedKeys = []
      let defaultCheckedKeys = []
      data.forEach(v => {
        defaultExpandedKeys.push(v.defaultId)
        v.nodes.forEach(child => {
          if (child.selected) {
            defaultCheckedKeys.push(child.defaultId)
          }
        })
      })
      console.log(defaultCheckedKeys, 'defaultCheckedKeys')
      this.setState(() => ({
        projectNode: data,
        defaultExpandedKeys,
        defaultCheckedKeys
      }))
    })
  }
   // 取消
   handleCancel() {
    this.props.closeStagePresetModal()
  }
  // 项目阶段设置确认选择
  handleConfirm () {
    if (this.selectedId.length === 0) {
      message.warning('请至少选择一个阶段节点', 1)
      return
    }
    postProjectPhases(this.projectId, this.state.selectNode).then((res) => {
      if (res.status === 200) {
        message.info('操作成功', 1)
        this.props.closeStagePresetModal(true);
      }
    })
  }

  onCheck(checkedKeys) {
    this.selectedId = checkedKeys.checked
    let datas =JSON.parse(JSON.stringify(this.state.projectNode))
    datas.forEach((val) => {
      val.nodes.forEach(child => {
        if (this.selectedId.indexOf(child.defaultId) > -1) {
          child.userSelected = true
        } else {
          child.userSelected = false
        }
      })
    })
    this.setState(() => ({selectNode: datas}))
  }
  render() {
    const { projectNode } = this.state;
    const { stagePresetModal} = this.props
    const loop = data => data.map((item) => {
      if (item.nodes && item.nodes.length) {
        return <TreeNode key={item.defaultId} title={item.name}>{loop(item.nodes)}</TreeNode>;
      }
      return <TreeNode key={item.defaultId} title={item.name}/>;
    });
    return (
      <div className='stage_preset_wrapper'>
        <Modal title='请选择项目节点'
            visible={stagePresetModal}
            onOk={this.handleConfirm}
            onCancel={this.handleCancel}
          >
            <div className='stage_content_list'>
              <Tree
              checkable
              defaultExpandedKeys={this.state.defaultExpandedKeys}
              checkStrictly={true}
              defaultCheckedKeys={this.state.defaultCheckedKeys}
              onCheck={this.onCheck}
              className="stage_preset_tree"
              >
                {
                  loop(projectNode)
                }
              </Tree>
            </div>
        </Modal>
      </div>
    )
  }
}
export default StagePreset