import './style.scss'
import React, { Component } from 'react';
import { Card, Form, Input, Select, Checkbox, DatePicker, TreeSelect , Button, message, Icon} from 'antd';
import moment from 'moment'
import {getProjectPhases, getProviderList, postAddTask, postUpload, openLoading} from "../../../utils/api"
import CheckBoxUser from '@/components/task/checkBoxUser'

const FormItem = Form.Item
const Option = Select.Option
const { TextArea } = Input
const SHOW_PARENT = TreeSelect.SHOW_PARENT
class addTask extends Component {
  constructor (props) {
    super(props)
    this.state = {
      treeData: [], // 节点树
      cooperatorPartake: true,
      cooperatorVisible: true,
      priority: 1,
      attachments: [], // 附件
      attachmentsIds: [], // 附件ID
      targets: [], // 选择的服务者
      ProviderList: [], // 服务者列表
      transferModal: false
    }
    this.projectId = this.props.match.params.projectId
    this.checkboxChange = this.checkboxChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.levelValue = this.levelValue.bind(this)
    this.targetsConfirm = this.targetsConfirm.bind(this)
    this.uploadaAtachments = this.uploadaAtachments.bind(this)
  }

  componentDidMount() {
    getProjectPhases(this.projectId).then((res) => {
      const datas = res.data.phases
      let tData = []
      datas.forEach((i, v) => {
        if (i.nodes.length) {
          tData.push({title: i.name, value: i.defaultId, key: i.defaultId, children: [], disableCheckbox: true})
          i.nodes.forEach((x) => {
            tData[v].children.push({title: x.name, value: x.id, key: x.id, isLeaf: true})
          })
        }
      })
      this.setState(() => ({
        treeData: tData
      }))
    })
    getProviderList().then((res) => {
      this.setState(() => ({
        ProviderList: res.data
      }))
    })
  }

  handleSubmit (e) {
    e.preventDefault()
    this.props.form.validateFields((err, fieldsValue) => {
      if (!err) {
        const deadline = moment(fieldsValue.deadline, 'YYYY-MM-DD HH:mm:ss').valueOf()
        fieldsValue.deadline = deadline
        fieldsValue.priority = this.state.priority
        fieldsValue.cooperatorPartake = this.state.cooperatorPartake
        fieldsValue.cooperatorVisible = this.state.cooperatorVisible
        fieldsValue.targets = this.state.targets
        fieldsValue.attachments = this.state.attachmentsIds
        if (this.state.targets.length === 0) message.error('请至少选择一位服务者')
        else {
          openLoading()
          postAddTask(this.projectId, fieldsValue).then((r) => {
            message.success('添加成功')
            this.props.history.go(-1)
          })
        }
      }
    })
  }

  checkboxChange (checkedValues) {
    if (checkedValues.length === 1 && checkedValues[0] === 1) {
      this.setState(() => ({
        cooperatorPartake: false,
        cooperatorVisible: true
      }))
    } else if (checkedValues.length === 0) {
      this.setState(() => ({
        cooperatorPartake: false,
        cooperatorVisible: false
      }))
    } else if (checkedValues.length === 2) {
      this.setState(() => ({
        cooperatorPartake: true,
        cooperatorVisible: true
      }))
    }
  }

  delete (index) {
    let attachments = [...this.state.attachments]
    let attachmentsIds = [...this.state.attachmentsIds]
    attachments.splice(index, 1)
    attachmentsIds.splice(index, 1)
    this.setState((prevState) => ({
      attachments,
      attachmentsIds
    }))
  }

  disabledDate(current) {
    return current && current < moment().endOf('day');
  }

  levelValue (value) {
    this.setState(() => ({
      priority: value
    }))
  }

  targetsConfirm (values) {
    this.setState(() => ({
      targets: values
    }))
  }

  uploadaAtachments (e) {
    const file = e.target.files[0]
    const param = new FormData()
    param.append('file', file)
    postUpload(param).then((res) => {
      this.setState((prevState) => {
        return {
          attachmentsIds: [res.data.uuid, ...prevState.attachmentsIds],
          attachments: [{name: file.name}, ...prevState.attachments]
        }
      })
    })
  }

  render() {
    const tProps = {
      treeData: this.state.treeData,
      onChange: this.onChange,
      treeCheckable: true,
      disableCheckbox: true,
      showCheckedStrategy: SHOW_PARENT,
      searchPlaceholder: '请选择节点',
      style: {
        width: 300,
      }
    }
    const { getFieldDecorator } = this.props.form;
    const {transferModal, ProviderList} = this.state
    return (
      <div id={'addTask-box'}>
        <CheckBoxUser
          history={this.props.history}
          confirmSubmit={this.targetsConfirm}
          userList={ProviderList}
          transferModal={transferModal}
          closeTransferModle={() => this.setState(() => ({transferModal: false}))}
        >
        </CheckBoxUser>

        <div className={'content-box'}>
          <Card title="添加任务">
            <Form onSubmit={this.handleSubmit}>

              <FormItem label={'选 择 节 点 '}>
                {
                  getFieldDecorator('nodes', {
                    rules: [{ required: true, message: '请选择阶段节点!' }]
                  })(
                    <TreeSelect {...tProps} />
                  )
                }
              </FormItem>

              <FormItem label={'截 止 时 间 '}>
                {
                  getFieldDecorator('deadline',{
                    rules: [{ required: true, message: '请选择截止时间!' }]
                  })(
                    <DatePicker
                      format="YYYY-MM-DD HH:mm:ss"
                      disabledDate={this.disabledDate}
                      showTime={{ defaultValue: moment('00:00:00', 'HH:mm:ss') }}
                    />
                  )
                }
              </FormItem>

              <FormItem label={'任 务 内 容 '}>
                {
                  getFieldDecorator('content',{
                    rules: [{ required: true, message: '请输入任务内容!' }]
                  })(
                    <TextArea placeholder={'请输入任务内容'}/>
                  )
                }
              </FormItem>

              <FormItem label={'验 收 标 准 '}>
                {
                  getFieldDecorator('acceptanceCriteria',{
                    rules: [{ required: true, message: '请输入验收标准!' }]
                  })(
                    <TextArea placeholder={'请输入验收标准'}/>
                  )
                }
              </FormItem>

              <FormItem label={'任务优先级'}>
                <Select defaultValue="1" style={{ width: 60 }} onChange={this.levelValue}>
                  <Option value={1}>1</Option>
                  <Option value={2}>2</Option>
                  <Option value={3}>3</Option>
                </Select>
                <span style={{marginLeft: '10px'}}>(1为最高级)</span>
              </FormItem>

              <FormItem label={'指派服务者 '}>
                <Button onClick={() => this.setState(() => ({transferModal: true}))}>点击指派</Button>
                <span style={{marginLeft: '10px'}}>已指派{this.state.targets.length}位服务者</span>
              </FormItem>

              <FormItem label={'指派合作者 '}>
                <Checkbox.Group onChange={this.checkboxChange} defaultValue={[0, 1]}>
                  <Checkbox value={0}>指派合作者</Checkbox>
                  {!this.state.cooperatorPartake?<Checkbox value={1}>合作者可见</Checkbox>: ''}
                </Checkbox.Group >
              </FormItem>

              <FormItem label={'上 传 附 件 '}>
                <label htmlFor="uploadInput" className={'uploadLabel'}><Icon type="upload" />点击上传</label>
                <input type="file" id={'uploadInput'} style={{display: 'none'}} onChange={this.uploadaAtachments}/>
              </FormItem>

              <div className={'upload-area'}>
                {
                  this.state.attachments.map((v, index) => {
                    return (
                      <div key={index}>
                        <p>{v.name}<Icon type="close" onClick={this.delete.bind(this, index)} /></p>
                      </div>
                    )
                  })
                }
              </div>

              <FormItem>
                <Button type="primary" htmlType="submit">添加</Button>
              </FormItem>
            </Form>
          </Card>
        </div>
      </div>
    )
  }
}
addTask = Form.create()(addTask)
export default addTask;
