import './style.scss'
import React, { Component, Fragment } from 'react';
import { Card, Button, Modal, message, Input, Icon, Timeline, Tag, notification } from 'antd'
import {getTaskDetail, getTaskAttachments, getTaskChangelogs, closeTask, getTaskRecord, getProviderList, getTaskParticipant, assignTask, openLoading, closeLoading} from "/api";
import {formdateTime, downloadFile} from "../../../utils/util/common";
import CheckBoxUser from '@/components/task/checkBoxUser'
import Risk from '../taskRisk'

class taskDetail extends Component {
  constructor (props) {
    super(props)
    this.state = {
      remark: '', // 关闭任务附言
      taskDetail: {},
      attachments: [],
      changelogs: [],
      recordList: [], // 跟进记录
      modalVisible: false,
      modalType: '', // 根据传递参数显示对应组件
      transferModal:  false,
      targets: [], // 已指派的服务者
      ProviderList: [] //服务者列表
    }
    this.projectId = this.props.match.params.projectId
    this.taskId = this.props.match.params.taskId
    this.changelog = this.changelog.bind(this)
    this.closeTask = this.closeTask.bind(this)
    this.inputChange = this.inputChange.bind(this)
    this.setModalVisible = this.setModalVisible.bind(this)
    this.targetsConfirm = this.targetsConfirm.bind(this)
    this.downloadFileFuc = this.downloadFileFuc.bind(this)
  }

  componentDidMount() {
    openLoading()
    getTaskAttachments(this.projectId, this.taskId).then((res) => {
      this.setState(() => ({
        attachments: res.data
      }))
    })
    getTaskDetail(this.projectId, this.taskId).then((res) => {
      closeLoading()
      this.setState(() => ({
        taskDetail: res.data
      }))
    })
    getTaskChangelogs(this.projectId, this.taskId).then((res) => {
      this.setState(() => ({
        changelogs: res.data
      }))
    })
    getTaskRecord(this.projectId, this.taskId).then((res) => {
      this.setState(() => ({
        recordList: res.data
      }))
    })
    getProviderList().then((res) => {
      this.setState(() => ({
        ProviderList: res.data
      }))
    })
    getTaskParticipant(this.projectId, this.taskId).then((res) => {
      // 过滤合作者留下服务者
      let idsData = []
      const datas = res.data
      datas.forEach((v) => {
        if (v.role === 'SERVANT') {
          idsData.push(v.employeeId)
        }
      })
      this.setState(() => ({
        targets: idsData
      }))
    })
  }
  // 附件渲染
  attachments () {
    return (
      <Card title="任务附件">
        {this.state.attachments.map((v) => {
        let url = `${sessionStorage.filesUrl}${v.path}`
        return (
          <div className={'attachments-item'}  key={url}>
            <p>{v.name}</p>
            <p>{formdateTime(v.createdTime)}</p>
            <p onClick={() => {this.downloadFileFuc(url,v.name)}} style={{color: '#1890ff', cursor: 'pointer'}}
            >下载</p>
          </div>
        )
      })}
      </Card>
    )
  }

  inputChange (e) {
    const val = e.target.value
    this.setState(() => ({
      remark: val
    }))
  }

  downloadFileFuc(url, name) {
    downloadFile(url, name)
  }
  // 变更记录
  changelog () {
    return (
      <Card title="变更记录">
        {this.state.changelogs.map((v) => {
          return (
            <Card key={v.id}>
              <ul>
                <li><span className={'name'}>任务优先级：</span><span className={'content'}>{v.priority}</span></li>
                <li><span className={'name'}>任务内容：</span><span className={'content'}>{v.content}</span></li>
                <li><span className={'name'}>验收标准：</span><span className={'content'}>{v.acceptanceCriteria}</span></li>
                <li><span className={'name'}>截止日期：</span><span className={'content'}>{formdateTime(v.deadline)}</span></li>
                <li><span className={'name'}>变更日期：</span><span className={'content'}>{formdateTime(v.changeTime)}</span></li>
              </ul>
              {/*附件*/}
              {v.attachments.map((i) => {
                let url = `${sessionStorage.filesUrl}${i.path}`
                return(
                  <div key={i.id}>
                    <p className={'second-title'}>附件</p>
                    <div className={'attachments-item'}  key={url}>
                      <p>{i.name}</p>
                      <p>{formdateTime(i.createdTime)}</p>
                      <p onClick={() => {this.downloadFileFuc(url,i.name)}} style={{color: '#1890ff', cursor: 'pointer'}}
                      >下载</p>
                    </div>
                  </div>
                )
              })}
            </Card>
          )
        })}
      </Card>
    )
  }

  setModalVisible (bool, str) {
    this.setState(() => ({
      modalVisible: bool,
      modalType: str
    }))
  }
  // 关闭任务
  closeTask () {
    const {taskId} = this.props.match.params
    this.setModalVisible(false)
    closeTask(taskId, {status: 'CLOSED', remark: this.state.remark}).then(() => {
      message.success('已关闭')
    })
  }

  targetsConfirm (values) {
    assignTask(this.projectId, this.taskId, {targets: values}).then(() => {
      notification.success({
        message: '提示',
        description: '重新指派成功',
      })
    })
  }

  typeoneJsx (v) {
    return(
      <Fragment>
        <p>{v.content}</p>
        <p>时间：{formdateTime(v.time)}</p>
      </Fragment>
    )
  }

  typeTwoJsx (v) {
    return(
      <div className={'Recording-typeTwo'}>
        <div className={'nameAndjob'}><span style={{marginRight: '10px'}}>{v.userName}</span><Tag color="#108ee9">{v.userDeptName}</Tag></div>
        <div>跟进内容：
          <p className={'Recording-content'}>{v.content}</p>
        </div>
        <p>跟进方式: <span>{v.method}</span></p>
        <p>跟进对象: <span>{v.target}</span></p>
        <p>附件：{v.fileCount}</p>
        <p>跟进时间：{formdateTime(v.time)}</p>
      </div>
    )
  }

  retureJsxForRecording () {
    if (this.state.recordList.length) {
      return(
        <div className={'Recording-box'}>
          <Timeline>
            {
              this.state.recordList.map((v) => {
                return(
                  <Fragment key={v.id}>
                    {
                      v.type === 'MESSAGE'?<Timeline.Item>{this.typeoneJsx(v)}</Timeline.Item>: <Timeline.Item>{this.typeTwoJsx(v)}</Timeline.Item>
                    }
                  </Fragment>
                )
              })
            }
          </Timeline>
        </div>
      )
    } else {
      return(
        <p style={{textAlign: 'center'}}>暂无记录</p>
      )
    }

  }

  returnMOdalJsx () {
    if (this.state.modalType === 'close') {
      return(
        <div>
          <p>您确定要关闭该任务吗？</p>
          <Input.TextArea value={this.state.remark} placeholder={'关闭该任务的原因'} onChange={this.inputChange}/>
          <Button onClick={this.closeTask} style={{marginTop: '20px'}} type='primary'>确定</Button>
        </div>
      )
    } else if (this.state.modalType === 'risk') {
      return(
        <Risk projectId={this.projectId} taskId={this.taskId} setModalVisible={this.setModalVisible}/>
      )
    }
  }

  render() {
    const {projectName, projectOpPhase, projectOpNode, priority, content, acceptanceCriteria, assignTime, deadline, status,
      participantCompletedCount, participantCount, hasDelayApply, waitForReview} = this.state.taskDetail
      const {ProviderList, targets, transferModal} = this.state
    return (
      <div id={'taskDetail-box'}>

        <CheckBoxUser
          confirmSubmit={this.targetsConfirm}
          userList={ProviderList}
          targets={targets}
          transferModal={transferModal}
          closeTransferModle={() => this.setState(() => ({transferModal: false}))}
        >
        </CheckBoxUser>

        <Modal
          footer={null}
          visible={this.state.modalVisible}
          onCancel={() => this.setModalVisible(false)}
        >
          {this.returnMOdalJsx()}
        </Modal>

        <div className={'content-box'}>
          <div className={'header-box'}>
            <p>
              <Button onClick={() => this.props.history.push(`/home/task/edit/${this.projectId}/${this.taskId}`)}> <Icon type="edit" />修改任务</Button>
              <Button onClick={() => this.setState(() => ({transferModal: true}))}> <Icon type="select" />重新指派</Button>
              <Button onClick={() => {
                if (hasDelayApply) {
                  this.props.history.push(`/home/task/delay/${this.projectId}/${this.taskId}`)
                } else {
                  message.warning('当前还没有人员申请延期')
                }
              }}><Icon type="fall" />任务延期</Button>
              <Button onClick={() => {
                if (waitForReview) {
                  this.props.history.push(`/home/task/censor/${this.projectId}/${this.taskId}`)
                } else {
                  message.warning('只有当全部人员提交审查申请后才能进行审查')
                }
              }}> <Icon type="fund" />任务审查</Button>
              <Button onClick={() => this.setModalVisible(true, 'risk')}><Icon type="notification" />风险提醒</Button>
              <Button onClick={() => this.setModalVisible(true, 'close')}><Icon type="poweroff" />关闭任务</Button>
            </p>
          </div>
          <div className={'showdata-box'}>
            <div className={'showdata-box-left'}>
              <Card title="任务详情">
                <ul>
                  <li><span className={'name'}>项目名称：</span><span className={'content'}>{projectName}</span></li>
                  <li><span className={'name'}>所属项目阶段：</span><span className={'content'}>{projectOpPhase}</span></li>
                  <li><span className={'name'}>所属项目节点：</span><span className={'content'}>{projectOpNode}</span></li>
                  <li><span className={'name'}>优先级：</span><span className={'content'}>{priority} (1为最高级)</span></li>
                  <li><span className={'name'}>任务内容：</span><span className={'content'}>{content}</span></li>
                  <li><span className={'name'}>验收标准：</span><span className={'content'}>{acceptanceCriteria}</span></li>
                  <li><span className={'name'}>分配日期：</span><span className={'content'}>{formdateTime(assignTime)}</span></li>
                  <li><span className={'name'}>截止日期：</span><span className={'content'}>{formdateTime(deadline)}</span></li>
                  <li><span className={'name'}>状态：</span><span className={'content'}>{status}</span></li>
                  <li><span className={'name'}>参与人员：</span><span className={'content'}>{participantCount}</span></li>
                  <li><span className={'name'}>完成人员：</span><span className={'content'}>{participantCompletedCount}</span></li>
                </ul>
              </Card>
              {this.state.attachments.length > 0? this.attachments() : ''}
              {this.state.changelogs.length > 0?  this.changelog(): ''}
            </div>
            <div className={'showdata-box-right'}>
              <Card title="跟进记录">
                {this.retureJsxForRecording()}
              </Card>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
export default taskDetail;
