import React, { Component } from 'react';
import { List, Avatar, Button, message, Modal, Pagination } from 'antd'
import {getProgram, openLoading, closeLoading, deleteProgram, getProgramDetail } from '/api';
import AddForm from './addForm'
import EditForm from './editForm'
import './style.scss';
class program extends Component {
  constructor(props) {
    super(props)
    this.state = {
      data: [], // 方案列表
      total: 0,
      visible: false,
      programVisible: false,
      isEdit: false,
      editData: {},
      attachmentsList: [], // 单个解决方案的附件
    }
    this.currentPage = 0
    this.getProgramFuc = this.getProgramFuc.bind(this)
    this.handleCancel = this.handleCancel.bind(this)
    this.programCancel = this.programCancel.bind(this)
    this.editProgramFuc = this.editProgramFuc.bind(this)
    this.checkAttachments = this.checkAttachments.bind(this)
    this.deleteProgramFuc = this.deleteProgramFuc.bind(this)
    this.pageChange = this.pageChange.bind(this)
  }
  componentDidMount() {
    this.getProgramFuc()
  }
 // 获取方案
  getProgramFuc (page = 0) {
    openLoading()
    const obj = {
      paging: true,
      page,
      size: 10
    }
    getProgram(obj).then((res) => {
      closeLoading()
      const datas = res.data
      this.setState(() => ({
        data: datas.content,
        total: datas.totalElements
      }))
    })
  }
  // 编辑和新增的蒙层
  programCancel () {
    this.setState(() => ({programVisible: false}))
  }
  // 附件蒙层
  handleCancel () {
    this.setState(() => ({visible: false}))
  }
  // 方案附件
  checkAttachments (attachments) {
    if (attachments.length) {
      this.setState(() => ({attachmentsList: attachments, visible: true}))
    }
  }

  editProgramFuc (id) {
    this.setState(() => ({
      isEdit: true
    }))
    openLoading()
    getProgramDetail(id).then((res) => {
      closeLoading()
      this.setState(() => ({
        editData: {...res.data, id},
        programVisible: true
      }))
    })
  }

  deleteProgramFuc (id) {
    deleteProgram(id).then((res) => {
      message.success('删除成功')
      this.getProgramFuc(this.currentPage)
    })
  }

  pageChange (page) {
    this.getProgramFuc(page - 1)
  }

  returnJsx () {
    if (this.state.isEdit) {
      return(
        <EditForm programCancel={this.programCancel} editData={this.state.editData} getProgramFuc={this.getProgramFuc}/>
      )
    } else {
      return(
        <AddForm programCancel={this.programCancel} getProgramFuc={this.getProgramFuc}/>
      )
    }
  }

  render() {
    return(
      <div id={'program-box'}>
        <Modal
          title="解决方案"
          visible={this.state.programVisible}
          footer={null}
          onCancel={this.programCancel}
        >
          {
            this.returnJsx()
          }
        </Modal>

        <div className={'title'}>
          <p>解决方案</p>
          <Button style={{marginTop: '10px'}} onClick={() => this.setState(() => ({programVisible: true, isEdit: false}))}>添加方案</Button>
        </div>
        <div className={'list-content'}>
          <Modal
            title="附件列表"
            visible={this.state.visible}
            footer={[
              <Button key="back" type="primary" onClick={this.handleCancel}>确定</Button>
            ]}
            onCancel={this.handleCancel}>
            {
              this.state.attachmentsList.map((v) => {
                return(
                  <p key={v.id}><a href={`${sessionStorage.filesUrl}${v.path}`}>{v.name}</a></p>
                )
              })
            }
          </Modal>

          <List
            itemLayout="horizontal"
            dataSource={this.state.data}
            renderItem={item => (
              <List.Item actions={[<span className={'blue-color'} onClick={() => this.editProgramFuc(item.id)}>编辑</span>, <span className={'blue-color'} onClick={this.deleteProgramFuc.bind(this, item.id)}>删除</span>, <span className={'blue-color'} onClick={this.checkAttachments.bind(this, item.attachments)}>附件（{item.attachments.length}）</span>]}>
                <List.Item.Meta
                  avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
                  title={<strong>{item.keywords.join(' ')}</strong>}
                  description={item.content}
                />
              </List.Item>
            )}
          />
          <Pagination total={this.state.total} showQuickJumper style={{marginTop: '20px', textAlign: 'center'}} onChange={this.pageChange}/>
        </div>
      </div>
    )
  }
}
export default program
