import React, { Component } from 'react';
import { Link, withRouter, Redirect} from "react-router-dom";
import './index.scss';
import { Menu, Icon, Badge, Popconfirm} from 'antd';
import { connect } from 'react-redux';
import {getNotReadMsg} from '/api'
const SubMenu = Menu.SubMenu;

class Header extends Component {
  constructor (props) {
    super(props)
    this.state = {
      selectedKeys: []
    }
    this.signOut = this.signOut.bind(this)
    this.activeMenu = this.activeMenu.bind(this)
    this.messageList = this.messageList.bind(this)

  }
  componentDidMount(){
    let {location, history} = this.props
    if (location.pathname === '/login' && this.props.isLogin) {
      history.replace('/')
    }
    getNotReadMsg().then((res) => {
      const message = res.data
      let newMessageCount = 0
      message.forEach((v) => {
        newMessageCount += v.newMessage
      })
      this.setState(() => ({
        message: newMessageCount
      }))
    })
    this.activeMenu(location.pathname)
    history.listen((_location) => {
      this.activeMenu(_location.pathname)
    })
    
  }

  messageList () {
    this.props.history.push('/home/message/messageList')
  }

  // 导航高亮
  activeMenu(path) {
    const routesMapping = {
      '/home/project': '/home/project', // 项目
      '/home/task': '/home/task', // 任务
      '/poweroff': '/poweroff', //退出
      '/home/config/user': '/home/config/user', // 用户
      '/home/config/phase': '/home/config/phase',
      '/home/config/node': '/home/config/node',
      '/home/config/program': '/home/config/program',
      '/home/message/messageList': 'home/message/messageList',
      '/home/config/report': '/home/config/report' // 项目汇总表
    };
    let selectedKeys = []
    for (let key in routesMapping) {
      if (path.indexOf(key) === 0) {
        selectedKeys[0] = routesMapping[key];
        return
      }
    };
    this.setState(() => ({selectedKeys}))
  }
  signOut () {
    sessionStorage.removeItem('Token')
    this.props.history.replace('/login')
  }
  render() {
    const { isLogin } = this.props
    return (
      <div className="header_wrapper">
        {isLogin ? (
            <Menu
              theme="dark"
              mode="horizontal"
              defaultSelectedKeys={['/home/project']}
              selectedKeys={this.state.selectedKeys}
              style={{ lineHeight: '64px',background: 'rgba(76,143,255,1)' }}
              className="header_menu"
              onClick={this.handleClick}
            >
              <Menu.Item key="/home/project">
                  <Link to='/home/project'>项目管理</Link>
              </Menu.Item>
              <Menu.Item key="/home/task">
                  <Link to='/home/task'>任务管理</Link>
              </Menu.Item>
              <SubMenu key="/home/config" title={<span>功能设置</span>}>
                <Menu.Item key="/home/config/user" className='children_item'><Link to='/home/config/user'>用户设置</Link></Menu.Item>
                <Menu.Item key="/home/config/phase" className='children_item'><Link to={'/home/config/phase'}>阶段设置</Link></Menu.Item>
                <Menu.Item key="/home/config/node" className='children_item'><Link to={'/home/config/node'}>节点设置</Link></Menu.Item>
                <Menu.Item key="/home/config/program" className='children_item'><Link to={'/home/config/program'}>解决方案</Link></Menu.Item>
                <Menu.Item key="/home/config/report" className='children_item'><Link to={'/home/config/report'}>项目汇总表</Link></Menu.Item>
              </SubMenu>
              <Menu.Item key="/poweroff" className={'floatRight'}>
                <Popconfirm title='即将退出登录' onConfirm={this.signOut}>
                  <Icon type={'poweroff'} style={{ fontSize: '18px' }} />
                </Popconfirm>
              </Menu.Item>
              <Menu.Item key="home/message/messageList" className={'floatRight'}>
                <Badge count={this.state.message}>
                  <Icon type={'message'} style={{ fontSize: '18px' }} onClick={this.messageList}/>
                </Badge>
              </Menu.Item>
            </Menu>
        ) : <Redirect to='/login'/>}
      </div>
    )
  }
}
const Headers = withRouter(Header)
// 获取state数据状态
const mapToStateProps = (state) => ({
  isLogin: state.login.isLogin
});
export default connect(mapToStateProps, null)(Headers);
