import React, {Component} from 'react'
import './style.scss'
import { List, Avatar, message, Popconfirm, Pagination } from 'antd'
import {getMessageList, delMessage, openLoading, closeLoading} from '/api'
import {urlQuery} from "../../../utils/util/common";

class MessageList extends Component{
	constructor (props) {
		super(props)
		this.state = {
			list: [],
			total: 0,
			size: 10
		}
		this.lookOver = this.lookOver.bind(this)
		this.getMessageList = this.getMessageList.bind(this)
		this.delMessage = this.delMessage.bind(this)
		this.pageChange = this.pageChange.bind(this)
		this.SizeChange = this.SizeChange.bind(this)
		this.currentPage = 0
	}

	componentDidMount () {
		this.getMessageList()
	}

	getMessageList (page = 0) {
		openLoading()
		const obj = {
			page,
			size: this.state.size,
			paging: true,
			type: 'system'
		}
		getMessageList(obj).then((res) => {
      closeLoading()
			const datas = res.data
			this.setState(() => ({
				list: datas.content,
        total: datas.totalElements
			}))
		})
	}

	lookOver (url) {
		const decodeUrl = decodeURIComponent(url)
		const index = decodeUrl.indexOf('http')
		const newUrl = decodeUrl.substring(index)
		// 以上解析并截取url
		const pramas = urlQuery(newUrl)
		if (newUrl.indexOf('TOprojectDetail') > -1) {
			this.props.history.push(`/home/project/detail/${pramas.id}`)
		} else if (newUrl.indexOf('taskDetail') > -1) {
			this.props.history.push(`/home/task/taskDetail/${pramas.projectId}/${pramas.taskId}`)
		} else if (newUrl.indexOf('Postpone') > -1) {
			this.props.history.push(`/home/task/delay/${pramas.projectId}/${pramas.taskId}`)
		} else if (newUrl.indexOf('Projectinspect') > -1) {
      this.props.history.push(`/home/project/detail/${pramas.id}`)
		} else {
			message.info('暂无目标链接')
		}
	}

	delMessage (id) {
		delMessage(id).then((res) => {
			message.success('已删除')
			this.getMessageList(this.currentPage)
		})
	}

  pageChange (page) {
		this.currentPage = page - 1
		this.getMessageList(page - 1)
	}

  SizeChange (current, size) {
    this.currentPage = current - 1
		this.setState(() => ({
      size
		}), () => {
      this.getMessageList(current - 1)
		})
	}

	returnJSx (item) {
		if (item.link) {
			return(
				[
					<Popconfirm title="你将要删除该条消息?" onConfirm={() => this.delMessage(item.id)} okText="Yes" cancelText="No">
						<span className={'blueSpan'}>删除</span>
					</Popconfirm>,
					<span className={'blueSpan'} onClick={() => this.lookOver(item.link)}>查看详情</span>
				]
			)
		} else {
			return(
				[
					<Popconfirm title="你将要删除该条消息?" onConfirm={() => this.delMessage(item.id)} okText="Yes" cancelText="No">
						<span className={'blueSpan'}>删除</span>
					</Popconfirm>
				]
			)
		}
	}

	render () {
		return(
			<div id={'message-box'}>
				<h4 className={'h4'}>消息中心</h4>
					<List
	        className="demo-loadmore-list"
	        itemLayout="horizontal"
	        dataSource={this.state.list}
	        renderItem={item => (
	          <List.Item actions={this.returnJSx(item)}>
	              <List.Item.Meta
	                avatar={<Avatar src="https://zos.alipayobjects.com/rmsportal/ODTLcjxAfvqbxHnVXCYX.png" />}
	                title={<p className={item.read?'read': 'notRead'}>{item.title}</p>}
	                description={item.contentPreview}
	              />
	          </List.Item>
	        )}
	      />
        <Pagination
					total={this.state.total}
					showSizeChanger showQuickJumper
					style={{marginTop: '20px', textAlign: 'center'}}
					onChange={this.pageChange}
          onShowSizeChange={this.SizeChange}
				/>
			</div>
		)
	}
}
export default MessageList
