// 时间戳格式化成 yy-mm-dd
export const formDate = (date) => {
  var dates = new Date(date)
  var y = dates.getFullYear()
  var m = dates.getMonth() + 1
  m = m < 10 ? ('0' + m) : m
  var d = dates.getDate()
  d = d < 10 ? ('0' + d) : d
  return y + '-' + m + '-' + d
}
// 时间戳格式化成 yy-mm-dd mm:nn
export const formdateTime = (date) => {
  if (!date) return
  var dates = new Date(date)
  var y = dates.getFullYear()
  var m = dates.getMonth() + 1
  m = m < 10 ? ('0' + m) : m
  var d = dates.getDate()
  d = d < 10 ? ('0' + d) : d
  var h = dates.getHours()
  h = h < 10 ? ('0' + h) : h
  var minute = dates.getMinutes()
  minute = minute < 10 ? ('0' + minute) : minute
  return y + '-' + m + '-' + d + ' ' + h + ':' + minute
}
export const urlQuery = (search) => {
  if (search === void 0) {
    search = ''
  }
  return (function (querystring) {
    if (querystring === void 0) {
      querystring = '';
    }
    return (function (q) {
      return (querystring.split('&').forEach(function (item) {
        return (function (kv) {
          return kv[0] && (q[kv[0]] = kv[1]);
        })(item.split('='));
      }), q);
    })({});
  })(search.split('?')[1]);
}
export const downloadFile = (url, name = '默认') => {
  const xhr = new XMLHttpRequest();
  xhr.open('GET', url, true);
  xhr.responseType = 'blob';
  xhr.setRequestHeader('Authorization', `Bearer ${sessionStorage.Token}`);
  xhr.onload = () => {
    if (xhr.status === 201) {
      // 获取图片blob数据并保存
      let URL = window.URL || window.webkitURL
      let objectUrl = URL.createObjectURL(xhr.response)
      let a = document.createElement('a')
      a.style.display = 'none'
      a.href = objectUrl
      a.download = name
      document.body.appendChild(a)
      a.click()
      URL.revokeObjectURL(objectUrl)
      document.body.removeChild(a)
    }
  };
  xhr.send()
}
