import React, { Component } from 'react';
import { Provider } from 'react-redux'
import { LocaleProvider } from 'antd';
import zh_CN from 'antd/lib/locale-provider/zh_CN';
import store from './store';
import FirstLevel from '@/router/firstLevel';
import '@/assets/icon/iconfont.css' // 字体图标

if (process.env.NODE_ENV === 'production') {
  sessionStorage.filesUrl = 'https://api.360edian.com/files'
} else {
  sessionStorage.filesUrl = 'http://api.365edian.com/files'
}
class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      token: null,
    }
  }
  componentDidMount() {
    this.setState(() => {
      return {token : sessionStorage.Token}
    })
  }

  render() {
    return (
      <Provider store={store}>
        <LocaleProvider locale={zh_CN}>
            <FirstLevel/>
        </LocaleProvider>
      </Provider>
    );
  }
}
export default App;
