import React, { Component } from 'react';
import { Table, Form, Row, Col, Input, Button, Select  } from 'antd';
import { connect } from 'react-redux';
import { getProjectList, openLoading, closeLoading } from '@/utils/api'
import countryData from '@/utils/util/countryData'
import { formDate } from '@/utils/util/common'
import './index.scss';
import Region from '@/components/project/region'

const FormItem = Form.Item;
const Option = Select.Option;
// 国家数据
let country = []
countryData.map(v => country.push(...v.data))
// 表格配置
const tableState = {
  bordered: true,
  pagination: true,
  showHeader: true,
  size: "middle",
  rowKey: "id"
}
class Report extends Component {
  constructor(props) {
    super(props)
    this.state = {
      projectList: null,
      isChina: false,
      params: {
        page: 0,
        size: 10
      },
      pagination: { // 分页
        total: 0,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: (page, pageSize) => this.pageChange(page, pageSize),
        onShowSizeChange: (current, size) => this.sizeChange(current, size)
      }
    }
    this.selectData = {
      status: '',
      country: '',
      province: '',
      city: '',
      district:'',
      createdTimeBegin: '',
      keywords: ''
    }
    // 项目状态
    this.projectStatus = {
      CREATED:'待审核',
      IN_REVIEW:'审核中',
      PASSED:'审核通过',
      ANALYZED:'已设阶段',
      NOT_PASSED:'审核失败',
      COMPLETED:'已完成',
      CLOSED:'已关闭'
    }
    this.columns = [
      {
        dataIndex: 'name',
        title: '项目名称',
      },
      {
        dataIndex: 'region',
        title: '所在区域',
      },
      {
        dataIndex: 'phase',
        title: '项目阶段',
      },
      {
        dataIndex: 'product',
        title: '客户需求',
      },
      {
        dataIndex: 'createdTime',
        title: '报备时间',
      },
      {
        dataIndex: 'status',
        title: '状态',
      },
      {
        dataIndex: 'progress',
        title: '任务进度',
      },
      {
        title: '操作',
        dataIndex: 'id',
        render: (text) => {
          return <span onClick={ this.toDetail.bind(this, text) } style={{color: 'rgba(44,112,231,1)'}}>查看</span>
        },
      },
    ];
    this.handleSearch = this.handleSearch.bind(this)
    this.countryChange = this.countryChange.bind(this)
    this.statusChange = this.statusChange.bind(this)
    this.regionChange = this.regionChange.bind(this)
    this.dateChange = this.dateChange.bind(this)
    this.keywordsChange = this.keywordsChange.bind(this)
  }
  componentDidMount() {
    this.queryProjectList()
  }
  // 输入项目名称
  keywordsChange(e) {
    this.selectData.keywords = e.target.value
  }
   // 查看详情
  toDetail(id) {
    this.props.history.push(`/home/project/detail/${id}`)
  }
  // 翻页
  pageChange(page, pageSize) {
    let params = {
      size: pageSize,
      page: page -1
    }
    this.queryProjectList(params)
  }
  sizeChange(current, size) {
    let params = {
      size,
      page: current - 1
    }
    let datas = Object.assign({}, this.state.params, { size: size })
    this.setState(() => {
      return {params: datas}
    })
    this.queryProjectList(params)
  }
  // 查询项目列表
  queryProjectList(params = {page: 0, size: 10}) {
    openLoading()
    let datas = {
      paging: true,
      ...this.selectData,
      ...params
    }
    getProjectList(datas).then(res => {
      closeLoading()
      let { content, total} = res.data
      let pagination = Object.assign({}, this.state.pagination)
      pagination.total = total
      let phase = {APPROVAL: '项目立项阶段', DESIGN: '项目编制方案阶段', TENDERING: '项目投标阶段'}
      content.forEach(v => {
        v.status = this.projectStatus[v.status]
        v.phase = phase[v.phase]
        v.createdTime = formDate(v.createdTime)
      })
      this.setState(() => {
        return { projectList: content, pagination }
      })
    })
  }
  handleSearch() {
    this.queryProjectList(this.state.params)
  }
  // 国家
  countryChange(val) {
    if (val === '中国') {
      this.setState(() => {
        return {isChina: true}
      })
    } else {
      this.setState(() => {
        return {isChina: false}
      })
    }
    this.selectData = Object.assign({}, this.selectData, {country: val, province: '',city: '',district:'',} )
  }
  // 日期选择
  dateChange(item) {
    this.selectData.createdTimeBegin = item ? this.date(item) : '';
  }
  // 日期转换
  date (str) {
    let date = new Date()
    let old = ''
    switch (str) {
      case '1天内': old = date.setDate(date.getDate() - 1)
        break
      case '1周内': old = date.setDate(date.getDate() - 7)
        break
      case '1个月内': old = date.setMonth(date.getMonth() - 1)
        break
      case '1个季度内': old = date.setMonth(date.getMonth() - 3)
        break
      case '半年内': old = date.setMonth(date.getMonth() - 6)
        break
      default:
        old = date.setFullYear(date.getFullYear() - 1)
    }
    return old
  }
  // 状态选择
  statusChange(val) {
    this.selectData.status = val ? val : '';
  }
  // 地区选择
  regionChange (item) {
    let [province, city, district] = item ? item : {}
    this.selectData = Object.assign({}, this.selectData, { province, city, district })
  }
  render() {
    return (
    <div className="report_list_wrapper">
      <header className="header">
        项目汇总表
        <label htmlFor="" className="right_btn"><i className="iconfont icon-bj"></i>项目字段管理</label>
      </header>
      <main>
        <div className="filter_search">
          <Form
            layout='inline'
            className="ant-advanced-search-form"
          >
            <Row gutter={16}>
              
              <Col span={8}>
                <FormItem label='项目状态'>
                  <Select placeholder="请选择项目状态" style={{ width: 240 }} onChange={this.statusChange} allowClear>
                    <Option value="">所有</Option>
                    <Option value="CREATED">待审核</Option>
                    <Option value="IN_REVIEW">审核中</Option>
                    <Option value="PASSED">审核通过</Option>
                    <Option value="ANALYZED">已设阶段</Option>
                    <Option value="NOT_PASSED">审核失败</Option>
                    <Option value="CLOSED">已关闭</Option>
                    <Option value="COMPLETED">已完成</Option>
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label='项目名称'>
                  <Input placeholder="请输入项目名称" style={{ width: 240 }} onChange={this.keywordsChange}/>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label='报备日期'>
                    <Select placeholder="请选择报备日期" style={{ width: 240 }} onChange={this.dateChange} allowClear>
                      <Option value="1天内">1天内</Option>
                      <Option value="1周内">1周内</Option>
                      <Option value="1个月内">1个月内</Option>
                      <Option value="1个季度内">1个季度内</Option>
                      <Option value="半年内">半年内</Option>
                      <Option value="1年内">1年内</Option>
                    </Select>
                  </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label='所在国家'>
                  <Select placeholder="请选择项目区域" style={{ width: 240 }} onChange={this.countryChange} allowClear>
                    {
                      country.map(v => <Option value={v} key={v}>{v}</Option>)
                    }
                  </Select>
                </FormItem>
              </Col>
              {
                this.state.isChina ? (<Col span={8}>
                  <FormItem label='所在区域'>
                    <Region regionChange={this.regionChange}></Region>
                  </FormItem>
                </Col>): ''
              }
              
            </Row>
            <Row>
              <Col span={24} style={{ textAlign: 'left' }}>
                <Button type="primary" onClick={this.handleSearch} style={{width: '218px', margin: '20px 0 0 0'}}>筛选</Button>
              </Col>
            </Row>
          </Form>
        </div>
        <div className="list_table">
          <Table
            {...tableState}
            columns={this.columns}
            pagination={ this.state.pagination }
            dataSource={this.state.projectList}
          />
        </div>
      </main>
    </div>)
  }
}
export default connect(null, null)(Report)