import React, { Component } from 'react';
import {putDefaultNode} from '/api'
import { Button, Form, Input, InputNumber, message } from 'antd'
const FormItem = Form.Item
class EditForm extends Component {
  constructor(props) {
    super(props)
    this.phaseSubmit = this.phaseSubmit.bind(this)
  }

  phaseSubmit (e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        putDefaultNode({id: this.props.nodeData.id, ...values}).then((res) => {
          message.success('编辑成功')
          this.props.handleCancel()
          this.props.getDefaultNodeFuc()
        })
      }
    })
  }

  render() {
    const {getFieldDecorator} = this.props.form
      return(
      <Form onSubmit={this.phaseSubmit}>
        <FormItem label={'名称'}>
          {getFieldDecorator('name', {
            rules: [{ required: true, message: '请输入阶段名称!' }],
            initialValue: this.props.nodeData.name
          })(
            <Input/>
          )}
        </FormItem>
        <FormItem label={'排序'}>
          {getFieldDecorator('orderNumber', {
            rules: [{ required: true, message: '请输入正整数的排列序号!' }],
            initialValue: this.props.nodeData.orderNumber
          })(
            <InputNumber min={1}/>
          )}
        </FormItem>
        <Button htmlType='submit' type="primary">提交</Button>
      </Form>
    )
  }
}
export default Form.create()(EditForm)
