import React, { Component } from 'react';
import { Table, Form, Button, Input, Select, TreeSelect } from 'antd';
import './style.scss'
import {getTaskList, getNodeTree} from "/api";
import {formDate} from "../../../utils/util/common";
const FormItem = Form.Item
const Option = Select.Option
const SHOW_PARENT = TreeSelect.SHOW_PARENT
class Task extends Component {
  constructor (props) {
    super(props)
    this.loading = false
    this.state = {
      value: [],
      keywords: '',
      stausValue: 'ALL',
      nodeArr : [], // 节点数组
      dateValue : new Date(),
      taskList: [], // 数据列表
      loading: false,
      total: 0,
      treeData: [] // 节点树
    }
    this.columns = [{
      title: '任务状态',
      dataIndex: 'status',
      key: 'status',
      align: 'center'
    }, {
      title: '优先级',
      dataIndex: 'priority',
      key: 'priority',
      align: 'center'
    }, {
      title: '任务名称',
      dataIndex: 'content',
      key: 'content',
      align: 'center'
    }, {
      title: '项目名称',
      dataIndex: 'projectName',
      key: 'projectName',
      align: 'center'
    }, {
      title: '截止日期',
      key: 'deadline',
      dataIndex: 'deadline',
      align: 'center'
    }, {
      title: '操作',
      key: 'action',
      render: (item) =>  (
        <span>
          <Button onClick={this.view.bind(this, item)}>查看</Button>
        </span>
      ),
    }]
    this.SizeChange = this.SizeChange.bind(this)
    this.changePage = this.changePage.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.statusSelectChange = this.statusSelectChange.bind(this)
    this.dateSelectChange = this.dateSelectChange.bind(this)
    this.inputChange = this.inputChange.bind(this)
    this.nodeChange = this.nodeChange.bind(this)
  }
  componentDidMount() {
    this.getTaskList()
    this.getNodeTreeFuc()
  }

  view (record) {
    this.props.history.push(`/home/task/taskDetail/${record.projectId}/${record.id}`)
  }

  date (str) {
    let date = new Date()
    let old = ''
    switch (str) {
      case '0': old = date.setDate(date.getDate() - 1)
        break
      case '1': old = date.setDate(date.getDate() - 7)
        break
      case '2': old = date.setMonth(date.getMonth() - 1)
        break
      case '3': old = date.setMonth(date.getMonth() - 3)
        break
      case '4': old = date.setMonth(date.getMonth() - 6)
        break
      default:
        old = date.setFullYear(date.getFullYear() - 1)
    }
    return old
  }

  inputChange (e) {
    const a = e.target.value
    this.setState((prev) => ({
      keywords: a
    }))
  }

  changePage (page, pageSize) {
    this.getTaskList(page - 1, pageSize)
  }

  SizeChange (current, size) {
    this.getTaskList(current - 1, size)
  }

  statusSelectChange (value) {
    this.setState(() => ({
      stausValue: value
    }))
  }

  dateSelectChange (value) {
    this.setState(() => ({
      dateValue: value
    }))
  }
   // 节点选择
  nodeChange (values) {
    this.setState(() => ({
      nodeArr: values
    }))
  }

  handleSubmit (e) {
    e.preventDefault()
    this.getTaskList(0, 10)
  }

  getNodeTreeFuc () {
    getNodeTree().then((res) => {
      const datas = res.data.phases
      let tData = []
      datas.forEach((i, v) => {
        if (i.nodes.length) {
          tData.push({title: i.name, value: i.defaultId, key: i.defaultId, children: [], disableCheckbox: true})
          i.nodes.forEach((x) => {
            tData[v].children.push({title: x.name, value: x.defaultId, key: x.defaultId, isLeaf: true})
          })
        }
      })
      this.setState(() => ({
        treeData: tData
      }))
    })
  }

  getTaskList (page = 0, size = 10) {
    this.setState(() => ({
      loading: true
    }))
    const {dateValue, stausValue, nodeArr, keywords} = this.state
    const params = {
      keywords,
      nodeDefaultIds: nodeArr.join(','),
      createdTimeBegin: this.date(dateValue),
      type: stausValue
    }
    const obj = {
      paging: true,
      page,
      size,
      ...params
    }
    getTaskList(obj).then((res) => {
      if (!res) return
      const statusObj = {
        CREATED: '已分配',
        WAIT_REVIEW: '待审查',
        RISKY: '风险任务',
        APPLY_DELAY: '延期申请',
        EXPIRED: '已到期',
        CLOSED: '已关闭',
        COMPLETED: '已完成'
      }
      const datas = res.data.content
      datas.forEach((item) => {
        item.deadline = formDate(item.deadline)
        item.status = statusObj[item.status]
      })
      this.setState(() => ({
        taskList: datas,
        loading: false,
        total: res.data.total
      }))
    })
  }
  render() {
    const tProps = {
      treeData: this.state.treeData,
      onChange: this.nodeChange,
      treeCheckable: true,
      disableCheckbox: true,
      showCheckedStrategy: SHOW_PARENT,
      searchPlaceholder: '请选择节点',
      style: {
        width: 300,
      }
    }
    return (
      <div id={'taskList-box'}>
        <div className={'show-data-content'}>
          <div className={'data-filter'}>
            <Form layout='inline'>
              <FormItem label={'任务名称'}>
                <Input placeholder={'请输入任务名称'} value={this.state.value} onChange={this.inputChange}/>
              </FormItem>

              <FormItem label={'状态筛选'}>
                <Select style={{ width: 200 }} placeholder={'请选择状态'}
                   defaultValue={'ALL'}
                  onChange={this.statusSelectChange}
                  allowClear={true}>
                  <Option value="ALL">所有</Option>
                  <Option value="WAIT_REVIEW">待审查</Option>
                  <Option value="RISKY">风险任务</Option>
                  <Option value="APPLY_DELAY">延期申请</Option>
                  <Option value="EXPIRED">已到期</Option>
                  <Option value="CLOSED">已关闭</Option>
                  <Option value="COMPLETED">已完成</Option>
                </Select>
              </FormItem>

              <FormItem label={'阶段节点'}>
                <TreeSelect {...tProps} />
              </FormItem>

              <FormItem label={'分配日期'}>
                <Select style={{ width: 200 }} placeholder={'请选择日期'}
                  allowClear={true}
                  onChange={this.dateSelectChange}>
                  <Option value="0">一天内</Option>
                  <Option value="1">一周内</Option>
                  <Option value="2">一个月内</Option>
                  <Option value="3">一个季度内</Option>
                  <Option value="4">半年内</Option>
                  <Option value="5">1年内</Option>
                </Select>
              </FormItem>

              <FormItem>
                <Button
                  type="primary"
                  htmlType="submit"
                  onClick={this.handleSubmit}
                >
                  筛选
                </Button>
              </FormItem>

            </Form>
          </div>
          <div className={'data-content'}>
            <Table
              pagination={{
                total: this.state.total,
                showQuickJumper: true,
                showSizeChanger: true,
                onShowSizeChange: this.SizeChange,
                onChange: this.changePage
              }}
              rowKey={key => key.id}
              columns={this.columns}
              dataSource={this.state.taskList}
              loading={this.state.loading}/>
          </div>
        </div>
      </div>
    )
  }
}
export default Task;
