import React, { Component } from 'react';
import { Table, Row, Col, Select } from 'antd';
import './index.scss'
import {getProjecTask, getProjectDetail, openLoading, closeLoading} from "/api";
import {formdateTime} from "../../../utils/util/common";

const Option = Select.Option;
// 表格配置
const tableState = {
  bordered: true,
  pagination: true,
  showHeader: true,
  size: "middle",
  rowKey: "id"
}
class Task extends Component {
  constructor (props) {
    super(props)
    this.state = {
      projectTaskList: [],
      projectDetail: {}, // 项目概览
      statusValue: '', // 状态值
      pageSize: 10, // 每页条数
      pagination: { // 分页配置
        total: 0,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: (page, pageSize) => this.pageChange(page, pageSize),
        onShowSizeChange: (current, size) => this.sizeChange(current, size)
      }
    }
    // 项目id
    this.projectId = this.props.match.params.taskProjectId
    this.projectName = this.props.match.params.projectName
    this.toTaskDetail = this.toTaskDetail.bind(this)
    this.statusChange = this.statusChange.bind(this)
    this.searchResult = this.searchResult.bind(this)
    this.addTask = this.addTask.bind(this)
    
    
    // 列表任务状态
    this.status = {
      ALL: '所有',
      CREATED: '已分配',
      WAIT_REVIEW: '待审查',
      RISKY: '风险任务',
      APPLY_DELAY: '延期申请',
      EXPIRED: '已到期',
      CLOSED: '已关闭',
      COMPLETED: '已完成'
    }
    // 筛选状态项
    this.filterStatus = {
      ALL: '所有',
      NEAR_EXPIRED: '即将到期',
      EXPIRED: '已延期',
      WAIT_REVIEW: '待审查',
      COMPLETED: '已完成'
    }
    // 项目状态
    this.projectStatus = {
      CREATED:'待审核',
      IN_REVIEW:'审核中',
      PASSED:'审核通过',
      ANALYZED:'已设阶段',
      NOT_PASSED:'审核失败',
      COMPLETED:'已完成',
      CLOSED:'已关闭'
    }
    // 表格列
    this.columns = [{
        title: '任务状态',
        dataIndex: 'status',
        align: 'center'
      }, {
        title: '优先级',
        dataIndex: 'priority',
        align: 'center'
      }, {
        title: '任务名称',
        dataIndex: 'content',
        align: 'center'
      }, {
        title: '项目名称',
        dataIndex: '',
        align: 'center',
        render: (text) => {
          return <label>{this.projectName}</label>
        },
      },
      {
        title: '参与人数',
        dataIndex: 'participantCount',
        align: 'center'
      },{
        title: '完成人数',
        dataIndex: 'participantCompletedCount',
        align: 'center'
      },
      {
        title: '操作',
        dataIndex: 'id',
        render: text => <label onClick={ this.toTaskDetail.bind(this, text) } style={{color: 'rgba(44,112,231,1)'}}>查看</label>,
      },
    ]
  }
  componentDidMount() {
    this.queryProjectTask()
    this.queryProjectDetail()
  }
  // 去任务详情
  toTaskDetail(id) {
    this.props.history.push(`/home/taskDetail/${this.projectId}/${id}`)
  }
  // 状态选择
  statusChange(val) {
    this.setState(() => ({statusValue: val}))
  }
  // 筛选
  searchResult() {
    let params = {
      page: 0,
      size: this.state.pageSize,
    }
    this.queryProjectTask(params)
  }
   // 翻页
   pageChange(page, pageSize) {
    let params = {
      size: pageSize,
      page: page -1
    }
    this.queryProjectTask(params)
  }
  sizeChange(current, size) {
    let params = {
      size,
      page: current - 1
    }
    this.setState(() => ({pageSize: size}))
    this.queryProjectTask(params)
  }
  // 项目详情
  queryProjectDetail() {
    getProjectDetail(this.projectId, {overview: true}).then(res => {
      let datas = res ? res.data : {}
      this.setState(() => ({projectDetail: datas}))
    })
  }
  // 查询项目的全部任务
  queryProjectTask (params = {page: 0, size: 10}) {
    openLoading()
    const datas = {
      paging: true,
      type: this.state.statusValue,
      ...params
    }
    getProjecTask(this.projectId, datas).then(res => {
      closeLoading()
      if (!res || !res.data) return
      let data= res.data.content
      let pagination = this.state.pagination
      pagination.total = res.data.total
      data.map(v => (
        v.status = this.status[v.status]
      ))
      this.setState(() => ({
        projectTaskList: data,
        pagination
      }))
    })
  }
  // 添加任务
  addTask () {
    this.props.history.push(`/home/project/addtask/${this.projectId}`)
  }

  render() {
    const { projectDetail, pagination } = this.state
    return (
      <div className='project_taskList_box'>
        <div className='title'>
          任务列表
          <label htmlFor="" className="add_task" onClick={this.addTask}><i className="iconfont icon-bj"></i>添加任务</label>
        </div>
        <div className='show-data-content'>
          <div className='top_base_info'>
            <Row gutter={32}>
              <Col span={8} className='col_item'>项目名称：{this.projectName}</Col>
              <Col span={8} className='col_item'>报备日期：{formdateTime(projectDetail.createdTime)}</Col>
              <Col span={8} className='col_item'>审核通过日期：{projectDetail.passedTime ? formdateTime(projectDetail.passedTime) : '--'}</Col>
              <Col span={8} className='col_item'>项目阶段：{projectDetail.opPhase ? projectDetail.opPhase : '--'}</Col>
              <Col span={8} className='col_item'>审核状态：{this.projectStatus[projectDetail.status]}</Col>
              <Col span={8} className='col_item'>任务完成进度：{projectDetail.progress}</Col>
            </Row>
            <Row gutter={32} className='search_row'>
              <Col span={4} className='col_item'>
                <label htmlFor="">状态筛选：</label>
                <Select placeholder="请选择任务状态" style={{ width: 160 }} onChange={this.statusChange} allowClear>
                  {
                    Object.keys(this.filterStatus).map(key => (
                      <Option value={key} key={key}>{this.filterStatus[key]}</Option>
                    ))
                  }
                </Select>
              </Col>
              <Col span={20}> <p className="search" onClick={this.searchResult}>筛选</p></Col>
            </Row>
          </div>
          <div className={'data-content'}>
            <Table
              {...tableState}
              columns={this.columns}
              pagination={ pagination }
              dataSource={this.state.projectTaskList}/>
          </div>
        </div>
      </div>
    )
  }
}
export default Task;
