import React, { Component } from 'react';
import { List, Row, Col } from 'antd';
import { openLoading, closeLoading, getProjectList, getTaskList } from '@/utils/api'
import { formDate } from '@/utils/util/common'
import './index.scss';
import ProjectStatusItem from '@/components/project/ProjectStatusItem'
class Project extends Component {
  constructor(props) {
    super(props)
    this.state = {
      reviewList: null,
      reviewingList: null,
      taskList: []
    }
    this.toTaskList = this.toTaskList.bind(this)
  }
  componentDidMount() {
    this.queryProjectList('CREATED')
    this.queryProjectList('IN_REVIEW')
    this.queryTaskList('WAIT_REVIEW')
  }
  // 查询项目列表
  queryProjectList(status, page = 0) {
    openLoading();
    const params = {
      status,
      page,
      size:6
    }
    getProjectList(params).then(res => {
      if (!res) return
      closeLoading()
      let data = JSON.parse(JSON.stringify(res.data))
      data.forEach(item => {
        item.createdTime = formDate(item.createdTime)
      });
      if (status === 'CREATED') {
        this.setState(() => {
          return { projectList: data }
        })
      } else {
        this.setState(() => {
          return { reviewingList: data }
        })
      }

    })
  }
  // 查询任务列表
  queryTaskList(status, page = 0) {
    const params = {
      status,
      page,
      size:10
    }
    getTaskList(params).then(res => {
      if (!res || res.status !== 200) return
      let data = res.data
      let taskList = data.splice(0, 5)
      this.setState(() => {
        return { taskList }
      })
    })
  }
  // 查询项目列表
  toTaskList() {
    this.props.history.push('/home/task')
  }
  // 去任务详情
  toTaskDetail(taskId, projectId) {
    this.props.history.push(`/home/task/taskDetail/${projectId}/${taskId}`)
  }
  render() {
    return (
      <div className="project_wrapper">
        <Row gutter={120}>
          <Col span={12}>
            <ProjectStatusItem projectList={this.state.projectList} title="待审核项目" history={this.props.history}></ProjectStatusItem>
          </Col>
          <Col span={12}>
            <ProjectStatusItem projectList={this.state.reviewingList} title="审核中项目" history={this.props.history}></ProjectStatusItem>
          </Col>
        </Row>
        <div className="task_box">
          <Row gutter={120}>
            <Col span={12}>
              <div className="project_list">
                <div className="status">
                  <p className="status_name">待审查任务</p>
                  <p className="more" onClick={this.toTaskList}>more...</p>
                </div>
                <List
                  dataSource={this.state.taskList}
                  renderItem={item => (
                  <List.Item className="task_content" onClick={() => {this.toTaskDetail(item.id, item.projectId)}}>
                    <List.Item.Meta
                      title={item.content}
                      description={item.projectName}
                    />
                  </List.Item>
                  )}
                />
              </div>
            </Col>
          </Row>
        </div>
      </div>
    )
  }
}
export default Project;
