import './style.scss'
import React, { Component, Fragment } from 'react';
import {Radio, Input, Button, Form, DatePicker, Checkbox, Row, Tag, message} from 'antd'
import {getTaskPeople, putTaskCensor, putTaskStatusChange, openLoading} from "../../../utils/api";
import moment from 'moment'
const RadioGroup = Radio.Group
const FormItem = Form.Item
const CheckboxGroup = Checkbox.Group
class censor extends Component {
  constructor (props) {
    super(props)
    this.state = {
      remark: '',
      userlist: [],
      radio: 1,
      isCloseTask: 0
    }
    this.projectId = this.props.match.params.projectId
    this.taskId = this.props.match.params.taskId
    this.retunJsxRadio = this.retunJsxRadio.bind(this)
    this.radioChange = this.radioChange.bind(this)
    this.taskClosed = this.taskClosed.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
    this.inputChange = this.inputChange.bind(this)
    this.putTaskStatusChange = this.putTaskStatusChange.bind(this)
    this.putTaskCensor = this.putTaskCensor.bind(this)
  }
  componentDidMount() {
    openLoading()
    this.getTaskPeopleList()
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        values.deadline = moment(values.deadline, 'YYYY-MM-DD HH:mm:ss').valueOf()
        if (!this.state.radio && !this.state.remark) {
          message.error('请简短附言')
        } else if (this.state.radio) {
          this.putTaskStatusChange('COMPLETED')
        } else if (!this.state.radio && !this.state.isCloseTask) {
          this.putTaskCensor(values)
        } else {
          this.putTaskStatusChange('CLOSED')
        }
      }
    });
  }
  putTaskStatusChange (taskStaus) {
    let obj = {
      status: taskStaus,
      remark: this.state.remark
    }
    putTaskStatusChange(this.taskId, obj).then((res) => {
      if (taskStaus === 'CLOSED') {
        message.success('任务已关闭')
        this.props.history.go(-1)
      } else {
        message.success('任务已完成')
        this.props.history.go(-1)
      }
    })
  }

  putTaskCensor (params) {
    let obj = {
      ...params,
      remark: this.state.remark
    }
    putTaskCensor(this.taskId, obj).then((res) => {
        message.success('任务已延期')
        this.props.history.go(-1)
    })
  }

  getTaskPeopleList () {
    getTaskPeople(this.projectId, this.taskId).then((res) => {
      this.setState(() => ({
        userlist: res.data
      }))
    })
  }

  radioChange(e) {
    const val = e.target.value
    this.setState(() => ({
      radio: val
    }))
  }

  taskClosed(e) {
    const val = e.target.value
    this.setState(() => ({
      isCloseTask: val
    }))
  }

  inputChange (e) {
    const val = e.target.value
    this.setState(() => ({
      remark: val
    }))
  }

  returnJsxCheckBox () {
    const {getFieldDecorator} = this.props.form
    const obj = {
      SERVANT: '服务者',
      COOPERATOR: '合作者'
    }
    if (!this.state.isCloseTask) {
      return(
        <Fragment>
          <FormItem>
            {
              getFieldDecorator('deadline', {
                rules: [{ required: true, message: '请选择延期日期!' }]
              })(
                <DatePicker
                  showTime
                  format="YYYY-MM-DD HH:mm:ss"
                  placeholder="请选择延期的日期"
                />
              )
            }
          </FormItem>
          <FormItem>
            <div className={'List-people'}>
              {
                getFieldDecorator('targets', {
                  rules: [{ required: true, message: '请选择审查不合格的对象!' }]
                })(
                  <CheckboxGroup>
                    {
                      this.state.userlist.map((v) => {
                        return(
                          <Row key={v.id}>
                            <Checkbox value={v.id}>
                              <span className={'name'}>{v.name}</span>
                              <span className={'phone'}>{v.phone}</span>
                              <span className={'dept'}>{v.deptName}</span>
                              <Tag color='#108ee9'>{obj[v.role]}</Tag>
                            </Checkbox>
                          </Row>
                        )
                      })
                    }
                  </CheckboxGroup>
                )
              }
            </div>
          </FormItem>
        </Fragment>
      )
    }
  }

  retunJsxRadio () {
    if (!this.state.radio) {
      return (
        <div>
          <div className={'radio-box'}>
            <RadioGroup value={this.state.isCloseTask} onChange={this.taskClosed}>
              <Radio value={0}>任务延期</Radio>
              <Radio value={1}>任务关闭</Radio>
            </RadioGroup>
          </div>
          {
            this.returnJsxCheckBox()
          }
        </div>
      )
    }
  }
  render() {
    return (
      <div id={'censor-box'}>
        <h4>审查合格，任务将变成“已完成”状态</h4>
        <h4>审查不合格，您可以选择将此任务延期或关闭</h4>
        <div className={'radio-box'}>
          <RadioGroup value={this.state.radio} onChange={this.radioChange}>
            <Radio value={1}>审查合格</Radio>
            <Radio value={0}>审查不合格</Radio>
          </RadioGroup>
        </div>
        {
          this.retunJsxRadio()
        }
        <Form onSubmit={this.handleSubmit}>
          <FormItem>
            <Input.TextArea placeholder={'简短附言（不超过20个字）'} maxLength={20} value={this.state.remark} onChange={this.inputChange}/>
          </FormItem>
          <FormItem>
            <Button htmlType='submit'>提交</Button>
          </FormItem>
        </Form>
      </div>
    )
  }
}
export default Form.create()(censor);
