import React, { Component } from 'react'
import { Tree, Modal } from 'antd';
import './style.scss'

const TreeNode = Tree.TreeNode;

class TransferProject extends Component {
  constructor(props) {
    super(props)
    this.state = {
      userList: [] // 用户列表
    }
    this.selectedId = []
    this.handleCancel = this.handleCancel.bind(this)
    this.handleConfirm = this.handleConfirm.bind(this)
    this.selected = this.selected.bind(this)
  }
  componentDidMount() {

  }
   // 取消
   handleCancel() {
    this.props.closeTransferModle()
  }
  // 确认提交
  handleConfirm () {
    this.props.confirmSubmit(this.selectedId)
    this.props.closeTransferModle()
  }
  // 选中
  selected(checkedKeys) {
    this.selectedId = checkedKeys
  }
  render() {
    const { userList, targets } = this.props;
    this.selectedId = this.props.targets
    const { transferModal} = this.props
    const loop = data => data.map((item) => {
      if (item.employees && item.employees.length) {
        return <TreeNode key={item.id} title={item.name} selectable={false} disableCheckbox={true}>{loop(item.employees)}</TreeNode>;
      }
      const title = (name, phone) => (
        <div>{name} &nbsp;{phone}</div>
      )
      return <TreeNode key={item.id} title={title(item.name, item.phone)}/>
    });
    return (
      <div className='transfer_project_wrapper'>
        <Modal title='指派对象'
            visible={transferModal}
            onOk={this.handleConfirm}
            onCancel={this.handleCancel}
          >
            <div className='transfer_user_list'>
              <Tree
              onSelect={this.selected}
              className="user_tree"
              multiple
              defaultExpandAll={true}
              defaultSelectedKeys={targets}
              >
                {
                  loop(userList)
                }
              </Tree>
            </div>
        </Modal>
      </div>
    )
  }
}
export default TransferProject
