import React, { Component } from 'react';
import {postUpload} from '/api'
import {Icon} from 'antd'
import './style.scss'

class UploadComponent extends Component {
  constructor (props) {
    super(props)
    this.state = {
      fileList: this.props.fileList
    }
    this.uploadaAtachments = this.uploadaAtachments.bind(this)
    this.deleteFile = this.deleteFile.bind(this)
    this.resetUpload = this.resetUpload.bind(this)
  }

  uploadaAtachments (e) {
    const file = e.target.files[0]
    const param = new FormData()
    param.append('file', file)
    postUpload(param).then((res) => {
      if (res.status === 200) {
        let obj = {
          name: file.name
        }
        if (this.props.isEdit) {
          obj.file = res.data.uuid
        } else {
          obj.id = res.data.uuid
        }
        this.setState((prevState) => ({
          fileList: [obj, ...prevState.fileList]
        }), () => {
          console.log(this.props)
          this.props.getFileList(this.state.fileList)
        })
      }
    })
    e.target.value = ''
  }

  deleteFile (index) {
    const fileList = [...this.state.fileList]
    fileList.splice(index, 1)
    this.setState(() => ({
      fileList
    }),() => {
      this.props.getFileList(this.state.fileList)
    })
  }

  resetUpload () {
    this.setState(() => ({
      fileList: []
    }))
  }

  render() {
    return(
      <div id={'uploadComponent-box'}>
        <div>
          <label htmlFor="uploadInput" className={'uploadLabel'}><Icon type="upload" />点击上传</label>
          <input type="file" id={'uploadInput'} style={{display: 'none'}} onChange={this.uploadaAtachments}/>
        </div>

        <div className={'upload-area'}>
          {
            this.state.fileList.map((v, index) => {
              return (
                <div key={index}>
                  <p><span className={'fileName'}>{v.name}</span><Icon type="close" onClick={() => this.deleteFile(index)}/></p>
                </div>
              )
            })
          }
        </div>
      </div>
    )
  }
}
export default UploadComponent
