// 判断当前环境
// let { NODE_ENV } = process.env
const getIntactUrl = (url, bool = true) => {
  if (bool) {
    return `http://api.365edian.com/teamwork${url}`
  } else {
    return `http://api.365edian.com${url}`
  }
}
const getIntactUrlFroProduction = (url, bool = true) => {
  if (bool) {
    return `https://api.360edian.com/teamwork${url}`
  } else {
    return `https://api.360edian.com${url}`
  }
}
const finalUrl = (url, bool) => {
  // if (NODE_ENV === 'production') {
  //   return getIntactUrlFroProduction(url, bool)
  // } else {
  //   return getIntactUrl(url, bool)
  // }
  return getIntactUrl(url, bool)
}
export default finalUrl
