import React, { Component } from 'react';
import { Table } from 'antd';
import './index.scss';
const columns = [{
  dataIndex: 'createdTime',
}, {
  dataIndex: 'region',
}];
const tableState = {
  bordered: false,
  pagination: false,
  showHeader: false,
  size: "middle",
  rowKey: "id"
}
class ProjectStatusItem extends Component {
  constructor(props) {
    super(props)
    this.getMore = this.getMore.bind(this);
    this.toDetail = this.toDetail.bind(this)
  }
  componentDidMount() {
    
  }
  // 更多
  getMore() {
    this.props.history.push('/home/project/list')
  }
   // 查看项目详情
  toDetail(id) {
    this.props.history.push(`/home/project/detail/${id}`)
  }
  render() {
    return (
      <div className="projectStatusItem_wrapper">
        <div className="project_list">
            <div className="status">
                <p className="status_name">{this.props.title}</p>
                <p className="more" onClick={this.getMore}>more...</p>
              </div>
              <Table
                {...tableState}
                columns={columns}
                dataSource={this.props.projectList}
                onRow={(record) => {
                    return {
                      onClick: () => {this.toDetail(record.id)},  // 点击行
                    };
                }}
              />
          </div>
      </div>
    )
  }
}
export default ProjectStatusItem;
