/* eslint-disable */
import axios from 'axios'
import store from '@/store';
import * as C from '@/store/constants'
import {
  httpRequestList
} from './httpRequestList'
import finalUrl from './finalUrl'
import {errorFuc} from './httpError'
// 请求拦截
axios.interceptors.request.use(config => {
  if (sessionStorage.Token) config.headers.Authorization = `Bearer ${sessionStorage.Token}`
  return config
}, error => Promise.reject(error))
// 响应拦截
axios.interceptors.response.use(response => {
  if (response.status === 200 || response.status === 304) return response
},(error) => {
  closeLoading()
  return Promise.reject(errorFuc(error))
})

const opt = (options) => {
  const CancelToken = axios.CancelToken
  return axios({
    ...options,
    timeout: 15000,
    cancelToken: new CancelToken((c) => {
      httpRequestList.push(c)
    })
  })
}
/**
 * 配置axios并导出,finalUrl函数接收两个参数，url 和 一个布尔值
 * true代表基础路径 + '/teamwrok',false代表没有'/teamwork' 默认为true
 */

// 获取用户token
export const getUserToken = (params = {}) => opt({
  url: finalUrl(`/uaa/oauth/token?grant_type=password&client_id=test-client&username=${params.acc}&password=${params.pwd}&client_secret=XBShNkCXphbq`, false),
  method: 'post'
})
// 获取用户信息
export const getUserInfo = (params = {}) => opt({
  url: finalUrl(`/uaa/user`, false),
  method: 'get',
})
// loading
export const openLoading = () => {
  store.dispatch({type:C.OPEN_LOADING})
}
export const closeLoading = () => {
  store.dispatch({type:C.CLOSE_LOADING})
}

