import React, { Component, Fragment } from 'react';
import {getDefaultPhases, getPhasesSelectNode, deleteDefaultPhases, getDefaultNode, postPhasesAndNode} from "../../../utils/api";
import './style.scss';
import {Table, Button, Popconfirm, Modal, message, Icon} from 'antd'
import AddForm from './addForm'
import EditForm from './editForm'
class phase extends Component {
  constructor(props) {
    super(props)
    this.nodeColumns = [{
      title: '排序',
      dataIndex: 'orderNumber',
      key: 'orderNumber',
      align: 'center'
    }, {
      title: '节点名称',
      dataIndex: 'name',
      key: 'name',
      align: 'center'
    }, {
      title: '关联阶段数量',
      dataIndex: 'phaseCount',
      key: 'phaseCount',
      align: 'center'
    }]
    this.phaseColumns = [{
      title: '排序',
      dataIndex: 'orderNumber',
      key: 'orderNumber',
      align: 'center'
    }, {
      title: '阶段名称',
      dataIndex: 'name',
      key: 'name',
      align: 'center'
    }, {
      title: '下属节点数量',
      dataIndex: 'nodeCount',
      key: 'nodeCount',
      align: 'center'
    }, {
      title: '操作',
      align: 'center',
      key: 'action',
      render: (record) => (
        <Fragment>
          <Button onClick={this.phaseEdit.bind(this, record)}>编辑</Button>
          <Button onClick={this.associatedNode.bind(this, record)}>关联节点</Button>
          <Popconfirm title="确认删除？" okText="确认" cancelText="取消" onConfirm={this.deletePhase.bind(this,record)}>
            <Button>删除</Button>
          </Popconfirm>
        </Fragment>
      ),
    }]
    this.state = {
      loading: false,
      visible: false, // 编辑弹出框
      visibleModal: false, // 关联节点弹出框
      isEdit: false, // 当前是否是编辑状态
      phaseDataSource: [],
      nodeData: [],
      nodeTotal: 0,
      selectedRowKeys: [], // 用户点击选择的id数组
      phaseData: {}
    }
    this.currentPage = 0
    this.handleCancel = this.handleCancel.bind(this)
    this.associatedConfirm = this.associatedConfirm.bind(this)
    this.addPhase = this.addPhase.bind(this)
    this.getDefaultPhasesFuc = this.getDefaultPhasesFuc.bind(this)
    this.getdefaultNodeFuc = this.getdefaultNodeFuc.bind(this)
    this.changePage = this.changePage.bind(this)
  }
  componentDidMount() {
    this.getDefaultPhasesFuc()
  }

  getDefaultPhasesFuc() {
    const obj = {
      page: this.currentPage,
      size: 100
    }
    getDefaultPhases(obj).then((res) => {
      this.setState(() => ({
        phaseDataSource: res.data
      }))
    })
  }

  deletePhase (row) {
    deleteDefaultPhases(row.id).then((res) => {
      message.success('删除成功')
      this.getDefaultPhasesFuc()
    })
  }

  phaseEdit (record) {
    this.setState(() => ({
      isEdit: true,
      phaseData: record
    }), () => {
      this.setState(() => ({visible: true}))
    })
  }

  addPhase () {
    this.setState(() => ({
      visible: true,
      isEdit: false
    }))
  }

  associatedNode (record) {
    this.setState(() => ({
      loading: true
    }))
    this.getdefaultNodeFuc()
    this.setState(() => ({
      visibleModal: true,
      phaseData: record
    }), () => {
      getPhasesSelectNode(this.state.phaseData.id).then((res) => {
        this.setState(() => ({
          selectedRowKeys: res.data
        }))
      })
    })
  }

  getdefaultNodeFuc (page = 0) {
    const obj = {
      paging: true,
      page,
      size: 10,
    }
    getDefaultNode(obj).then((res) => {
      this.setState(() => ({
        nodeData: res.data.content,
        nodeTotal: res.data.totalElements,
        loading: false
      }))
    })
  }

  changePage (page) {
    this.getdefaultNodeFuc(page - 1)
  }

  associatedConfirm () {
    const {phaseData, selectedRowKeys} = this.state
    this.setState(() => ({
      visibleModal: false
    }))
    postPhasesAndNode(phaseData.id, {nodes: selectedRowKeys}).then((res) => {
      message.success('关联成功')
      this.getDefaultPhasesFuc()
    })
  }

  handleCancel () {
    this.setState(() => ({visible: false, visibleModal: false}))
  }


  editJsx () {
    if (this.state.isEdit) {
      return(
        <EditForm
          handleCancel={this.handleCancel}
          phaseData={this.state.phaseData}
          getDefaultPhasesFuc={this.getDefaultPhasesFuc}
        />
      )
    } else {
      return(<AddForm handleCancel={this.handleCancel} getDefaultPhasesFuc={this.getDefaultPhasesFuc}/>)
    }
  }

  render() {
    const nodeRowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        this.setState(() => ({
          selectedRowKeys
        }))
      },
      selectedRowKeys: this.state.selectedRowKeys
    }
    return (
      <div id={'phase-box'}>
        <div className={'title'}>
          阶段预设 <Button onClick={this.addPhase}> <Icon type="plus" />添加阶段</Button>
        </div>
        <div className={'table-content'}>

          <Modal
            title={this.state.isEdit?'编辑': '添加'}
            visible={this.state.visible}
            onCancel={this.handleCancel}
            footer={null}
          >
            {
              this.editJsx()
            }
          </Modal>

          <Modal
            title="关联节点"
            visible={this.state.visibleModal}
            onCancel={this.handleCancel}
            onOk={this.associatedConfirm}
            width={800}>
            <Table
              columns={this.nodeColumns}
              dataSource={this.state.nodeData}
              rowKey={key => key.id}
              loading={this.state.loading}
              pagination={{
                total: this.state.nodeTotal,
                onChange: this.changePage
              }}
              rowSelection={nodeRowSelection}
            />
          </Modal>

          <Table
            columns={this.phaseColumns}
            dataSource={this.state.phaseDataSource}
            rowKey={key => key.id}
            pagination={false}
            loading={this.state.loading}
          />
        </div>
      </div>
    )
  }
}
export default phase
