import React, { Component } from 'react';
import { Table, Form, Row, Col, Input, Button, Select, Modal, message, Radio, Checkbox } from 'antd';
import { connect } from 'react-redux';
import { openLoading, closeLoading, getUserArr, deleteUser, getDepartments, getProductLines, postAddUser, putEmployees } from '@/utils/api'
import './index.scss';

const FormItem = Form.Item;
const Option = Select.Option;
const confirm = Modal.confirm;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
// 国家数据
// 表格配置
const tableState = {
  bordered: true,
  pagination: true,
  showHeader: true,
  size: "middle",
  rowKey: "id"
}
class UserList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      employees: [], // 所有用户
      userDep: [], // 用户部门
      prodLineList: [], // 产线
      userVisible: false, // 用户弹窗
      disabled: false, // 按钮禁用
      isEdit: false, // 是否是编辑用户
      role: 'COORDINATOR', // 当前用户角色
      editItem: {}, // 编辑用户
      params: {
        page: 0,
        size: 10
      },
      pagination: { // 分页
        total: 0,
        current: 0,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: (page, pageSize) => this.pageChange(page, pageSize),
        onShowSizeChange: (current, size) => this.sizeChange(current, size)
      },
      columns: [ // 表格
        {
          dataIndex: 'role',
          title: '用户角色',
          render: (val) => {
            return <label>{roleStatus[val]}</label>
          },
        },
        {
          dataIndex: 'deptName',
          title: '用户部门',
        },
        {
          dataIndex: 'name',
          title: '用户姓名',
        },
        {
          dataIndex: 'sex',
          title: '性别',
          render: (val) => {
            return <label>{sex[val]}</label>
          },
        },
        {
          dataIndex: 'phone',
          title: '电话',
        },
        {
          dataIndex: 'email',
          title: '邮箱',
          render: (val) => {
            return <label>{val === '' || !val ? '--' :  val}</label>
          },
        },
        {
          title: '操作',
          render: (item) => {
            return <p><label className='edit_user operate' onClick={() => this.editUser(item)}>编辑</label> <label className='del_user operate' onClick={() => this.deleteUser(item.id)}>删除</label></p>
          },
        },
      ]
    }
    this.selectData = {
      roles: '',
      deptId: '',
      name: ''
    }
    // 项目状态
    const roleStatus = {
      COORDINATOR:'项目管家',
      SERVANT:'服务者',
    }
    const sex = ['女', '男']
    
    this.handleSearch = this.handleSearch.bind(this)
    this.roleChange = this.roleChange.bind(this)
    this.depChange = this.depChange.bind(this)
    this.keywordsChange = this.keywordsChange.bind(this)
    this.deleteUser = this.deleteUser.bind(this)
    // 弹窗
    this.handleCancel = this.handleCancel.bind(this)
    this.handleConfirm = this.handleConfirm.bind(this)
    this.showAddUserModal = this.showAddUserModal.bind(this)
    this.userRoleChange = this.userRoleChange.bind(this) // 角色改变
    // 编辑用户
    this.editUser = this.editUser.bind(this)
  }
  componentDidMount() {
    this.getUser()
    this.queryDep() // 获取部门
    this.queryProductLine()
  }
  // 输入项目名称
  keywordsChange(e) {
    this.selectData.name = e.target.value
  }
  // 获取用户列表
  getUser (params) {
    openLoading()
    const datas = {
      paging: true,
      ...this.selectData,
      ...this.state.params,
      ...params
    }
    getUserArr(datas).then((res) => {
      closeLoading()
      if (!res) return
      const { content, totalElements} = res.data
      let pagination = Object.assign({}, this.state.pagination, {total: totalElements})
      this.setState(() => ({pagination}))
      this.setState(() => ({employees: content}))
    })
  }
  // 获取部门
  queryDep() {
    getDepartments().then(res => {
      if (!res) return
      const userDep = res.data
      this.setState(() => ({userDep}))
    })
  }
  // 获取产线
  queryProductLine() {
    getProductLines().then(res => {
      if (!res) return
      let prodLineList = res.data
      let prodLineArr = (val) => {
        let prodLineName = []
        prodLineList.forEach(item => {
          if (val.includes(item.id)) {
            prodLineName.push(item.abbreviation)
          }
        })
        return prodLineName
      }
      let prodLine = {
        dataIndex: 'productLineIds',
        title: '产线（简称）',
        render: (val) => {
          return <label>{val.length === 0 ? '--' :  prodLineArr(val).toString()}</label>
        },
      }
      let columns = this.state.columns
      columns.splice(2,0, prodLine)
      this.setState(() => ({prodLineList, columns}))
    })
  }
  // 删除用户
  deleteUser (id) {
    confirm({
      title: '确定删除该用户吗?',
      okText: '确认',
      cancelText: '取消',
      onOk: () => {
        this.delFun(id)
      }
    });
  }
  delFun(id) {
    deleteUser(id).then((res) => {
      if (res.status === 200) {
        message.info('删除成功', 1)
        this.getUser()
      }
    })
  }
  // 翻页
  pageChange(page, pageSize) {
    let params = {
      size: pageSize,
      page: page -1
    }
    this.setPagination(page)
    this.getUser(params)
  }
  sizeChange(current, size) {
    let params = {
      size,
      page: current - 1
    }
    let datas = Object.assign({}, this.state.params, { size: size })
    this.setState(() => {
      return {params: datas}
    })
    this.getUser(params)
  }
  // 筛选
  handleSearch() {
    let params = {
      page: 0,
      ...this.selectData
    }
    this.setPagination()
    this.getUser(params)
  }
  // 设置当前页
  setPagination(current = 0) {
    let pagination = Object.assign({}, this.state.pagination, {current})
    this.setState(() => ({pagination}))
  }
  // 角色选择
  roleChange(val) {
    this.selectData.roles = val ? val : '';
  }
   // 部门选择
  depChange(val) {
    this.selectData.deptId = val ? val : '';
  }
  // 用户操作弹窗
  // 取消
  handleCancel() {
    this.clearForm()
    this.setState(() => ({userVisible: false}))
  }
  // 用户角色切换
  userRoleChange(val) {
    let role = val ? val : '';
    this.setState(() => ({role }))
  }
  // 确认提交
  handleConfirm(e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (!this.state.isEdit) {
          this.postUserInfo(values)
          return
        }
        this.editUserFunc(values)
      }
    });
  }
  // 验证电话规则
  validateTel(rule, value, callback) {
    let reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
    if (!reg.test(value)) { // 正则验证
      callback('请输入正确的11位手机号'); 
    } else {
      callback()
    }
  }
  // 验证邮箱规则
  validateEmail(rule, value, callback) {
    const reg = /^[A-z0-9_]{3,12}@[A-z0-9_]{2,12}(\.com|\.cn|\.com\.cn)$/g
    if (value === '' || !value) {
      callback()
    } else if (!reg.test(value)) { // 正则验证
      callback('请输入正确的邮箱地址'); 
    } else {
      callback()
    }
  }
  // 提交fun
  postUserInfo (formData) {
    openLoading()
    this.setState(() => ({disabled: true}))
    postAddUser(formData).then((res) => {
      closeLoading()
      this.setState(() => ({disabled: false}))
      if (res.status === 200) {
        message.info('操作成功')
        this.clearForm()
        this.setState(() => ({userVisible: false}))
        this.getUser()
      }
    }).catch(() => {
      this.setState(() => ({disabled: false}))
    })
  }
  // 编辑用户func
  editUserFunc(params) {
    openLoading()
    putEmployees(this.state.editItem.id, params).then((res) => {
      closeLoading()
      if (res.status === 200) {
        message.info('操作成功')
        this.clearForm()
        this.setState(() => ({userVisible: false}))
        this.getUser()
      }
    })
  }
  // 清除表单数据
  clearForm() {
    this.setState(() => ({isEdit: false}))
    this.props.form.resetFields()
  }
  // 编辑用户
  editUser(item) {
    this.setState(() => ({isEdit: true, editItem: item, userVisible: true, role: item.role}))
  }
  // 新增用户&编辑
  addUserRender(isEdit = false) {
    const title = isEdit ? '编辑用户' : '添加用户'
    const { getFieldDecorator } = this.props.form;
    let { userDep, prodLineList, disabled, editItem, role } = this.state
    return (
      <Modal title={title}
        visible={this.state.userVisible}
        footer={null}
        className='add_user_modal'
        onCancel={this.handleCancel}
      >
        <Form
            layout='inline'
            className="ant-advanced-search-form"
            onSubmit={this.handleConfirm}
          >
          <FormItem label='性别选择'>
            {
              !isEdit ?(getFieldDecorator('sex', {
                rules: [{
                  required: true,
                  message: '请选择角色',
                }],
                initialValue: 1
              })(
                <RadioGroup>
                  <Radio value={1}>男</Radio>
                  <Radio value={0}>女</Radio>
                </RadioGroup>
              )) : (<RadioGroup value={editItem.sex}>
                <Radio value={1} disabled>男</Radio>
                <Radio value={0} disabled>女</Radio>
              </RadioGroup>)
              }
          </FormItem>
          <FormItem label='角色选择'>
            {getFieldDecorator('role', {
              rules: [{
                required: true,
                message: '请选择角色',
              }],
              initialValue: isEdit ? editItem.role: '项目管家'
            })(
              <Select placeholder="请选择角色" style={{ width: 280 }} onChange={this.userRoleChange}>
                <Option value="COORDINATOR">项目管家</Option>
                <Option value="SERVANT">服务者</Option>
              </Select>
            )}
          </FormItem>
          {
            role !== 'COORDINATOR' ? '' : (
              <FormItem label='请选择产线' style={{ width: '100%'}}>
                {getFieldDecorator('productLineIds', {
                  rules: [{
                    required: true,
                    message: '请选择产线',
                  }],
                  initialValue: isEdit ? editItem.productLineIds: []
                })(
                  <CheckboxGroup style={{ width: '100%',lineHeight: '32px'}}>
                    <Row>
                      {
                        prodLineList.map((item) => (
                          <Col span={12} key={item.id}><Checkbox value={item.id}>{item.abbreviation}</Checkbox></Col>
                        ))
                      }
                    </Row>
                  </CheckboxGroup>
                )}
              </FormItem>
            )
          }
          <FormItem label='所属部门'>
            {getFieldDecorator('departmentId', {
                rules: [{
                  required: true,
                  message: '请选择所属部门',
                }],
                initialValue: isEdit ? editItem.deptId: undefined
              })(
                <Select placeholder="请选择所属部门" style={{ width: 280 }}>
                  {
                    userDep.map((item) => (
                      <Option value={item.id} key={item.id}>{item.name}</Option>
                    ))
                  }
                </Select>
              )}
          </FormItem>
          <FormItem label='注册号码'>
            {
              !isEdit ? (getFieldDecorator('tel', {
                rules: [{
                  required: true,
                  message: '请输入注册号码',
                },{
                  validator: this.validateTel
                }],
              })(
                <Input placeholder="请输入注册号码" style={{ width: 280 }}/>
              )) : <Input placeholder="请输入注册号码" value= {editItem.phone} style={{ width: 280 }} disabled={isEdit? true:false}/>
            }
          </FormItem>
          <FormItem label='用户姓名'>
            {getFieldDecorator('name', {
                rules: [{
                  required: true,
                  message: '请输入用户姓名',
                }],
                initialValue: isEdit ? editItem.name: ''
              })(
                <Input placeholder="请输入用户姓名" style={{ width: 280 }}/>
            )}
          </FormItem>
          <FormItem label='邮箱地址'>
              {getFieldDecorator('email', {
                rules: [{
                  required: false,
                },{
                  validator: this.validateEmail
                }],
                initialValue: isEdit ? editItem.email: ''
              })(
                <Input placeholder="请输入邮箱地址" style={{ width: 280 }}/>
              )}
          </FormItem>
          <div style={{textAlign: 'center', width: '60%', margin: '30px auto'}}>
            <Button type="primary" htmlType="submit" block disabled={disabled}>提交</Button>
          </div>
        </Form>
      </Modal>
    )
  }
  showAddUserModal() {
    this.setState(() => ({userVisible: true, role: 'COORDINATOR'}))
  }
  render() {
    return (
    <div className="user_list_wrapper">
      <header className="header">
        <div className='title'>
          用户列表
          <label htmlFor="" className="add_user" onClick={this.showAddUserModal}><i className="iconfont icon-bj"></i>添加用户</label>
        </div>
      </header>
      <main>
        <div className="filter_search">
          <Form
            layout='inline'
            className="ant-advanced-search-form"
          >
            <Row gutter={16}>
              <Col span={8}>
                <FormItem label='选择角色'>
                  <Select placeholder="请选择角色" style={{ width: 240 }} onChange={this.roleChange} allowClear>
                    <Option value="">所有角色</Option>
                    <Option value="COORDINATOR">项目管家</Option>
                    <Option value="SERVANT">服务者</Option>
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label='选择部门'>
                  <Select placeholder="请选择部门" style={{ width: 240 }} onChange={this.depChange} allowClear>
                    <Option value="">所有部门</Option>
                    {
                      this.state.userDep.map((v, index) => (
                        <Option value={v.id} key={index}>{v.name}</Option>
                      ))
                    }
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label='用户名称'>
                  <Input placeholder="请输入用户名称" style={{ width: 240 }} onChange={this.keywordsChange}/>
                </FormItem>
              </Col>
            </Row>
            <Row className="search_btn">
              <Col span={24} >
                  <FormItem label=''>
                    <Button type="primary" onClick={this.handleSearch} style={{width: '218px'}}>筛选</Button>
                </FormItem>
              </Col>
            </Row>
          </Form>
        </div>
        <div className="list_table">
          <Table
            {...tableState}
            columns={this.state.columns}
            pagination={ this.state.pagination }
            dataSource={this.state.employees}
          />
        </div>
      </main>
      {this.addUserRender(this.state.isEdit)}
    </div>)
  }
}

const WrappedUserList = Form.create()(UserList);
export default connect(null, null)(WrappedUserList)