import React from 'react';
import {Route ,Switch} from 'react-router-dom'
// 项目
import Project from '@/pages/project/projectStatusList'
import ProjectList from '@/pages/project/list'
import ProjectDetail from '@/pages/project/detail/detail'
import ProjectTaskList from '@/pages/project/projectTaskList'

// 任务
import TaskList from '@/pages/task/taskList/task'
import TaskDetail from '@/pages/task/taskDetail/taskDetail'
import AddTask from '@/pages/task/addTask/addTask'
import EditTask from '@/pages/task/editTask'
import TaskCensor from '@/pages/task/taskCensor'
import TaskRisk from '@/pages/task/taskRisk'
import TaskDelay from '@/pages/task/taskDelay'

// 设置
import UserList from '@/pages/config/user'
import Phase from '@/pages/config/phase'
import Report from '@/pages/config/report/reportList'
import Node from '@/pages/config/node'
import Program from '@/pages/config/program'
// 消息
import Message from '@/pages/message/messageList'
const SecondLevel = (props) => (
  <Switch>
      {/*任务*/}
    <Route path="/home/task"  exact={true} component={TaskList}/>
    <Route path="/home/task/taskDetail/:projectId/:taskId"  exact={true} component={TaskDetail}/>
    <Route path="/home/project/addTask/:projectId" exact={true} component={AddTask}/>
    <Route path="/home/task/edit/:projectId/:taskId" exact={true} component={EditTask}/>
    <Route path="/home/task/censor/:projectId/:taskId"  exact={true} component={TaskCensor}/>
    <Route path="/home/task/risk/:projectId/:taskId"  exact={true} component={TaskRisk}/>
    <Route path="/home/task/delay/:projectId/:taskId"  exact={true} component={TaskDelay}/>
      {/*项目*/}
    <Route path="/home/project"  exact={true} component={Project}/>
    <Route path="/home/project/list"  exact component={ProjectList}/>
    <Route path="/home/project/detail/:projectId"  exact component={ProjectDetail}/>
    <Route path="/home/project/projectTaskList/:taskProjectId/:projectName"  exact={true} component={ProjectTaskList}/>
    {/* 设置 */}
    <Route path="/home/config/user"  exact={true} component={UserList}/>
    <Route path="/home/config/phase"  exact={true} component={Phase}/>
    <Route path="/home/config/report"  exact={true} component={Report}/>
    <Route path="/home/config/node"  exact={true} component={Node}/>
    <Route path="/home/config/program"  exact={true} component={Program}/>
    {/*消息*/}
    <Route path="/home/message/messageList"  exact={true} component={Message}/>
  </Switch>
);
export default SecondLevel;
