import './style.scss'
import React, { Component } from 'react';
import {Input, Button, Checkbox, Row, Tag, Form, message} from 'antd'
import {getTaskPeople, putTaskRisk} from "../../../utils/api";
const FormItem  = Form.Item
const CheckboxGroup = Checkbox.Group
class Task extends Component {
  constructor (props) {
    super(props)
    this.state = {
      userlist: []
    }
    this.projectId = this.props.projectId
    this.taskId = this.props.taskId
    this.getUserList = this.getUserList.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }
  componentDidMount() {
    this.getUserList()
  }

  getUserList () {
    if (this.state.userlist.length === 0) {
      getTaskPeople(this.projectId, this.taskId).then((r) => {
        this.setState(() => ({
          userlist: r.data
        }))
      })
    }
  }

  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        putTaskRisk(this.taskId, {risky: true, ...values}).then((res) => {
          if (res.status === 200) {
            message.success('提醒成功')
            this.props.setModalVisible(false)
          }
        })
      }
    });
  }

  retureJsx () {
    const obj = {
      SERVANT: '服务者',
      COOPERATOR: '合作者'
    }
    return(
      <CheckboxGroup>
        {
          this.state.userlist.map((v) => {
            return(
              <Row key={v.userId}>
                <Checkbox value={v.id?v.id: ''}>
                  <span className={'name'}>{v.name}</span>
                  <span className={'phone'}>{v.phone}</span>
                  <span className={'dept'}>{v.deptName}</span>
                  <Tag color='#108ee9'>{obj[v.role]}</Tag>
                </Checkbox>
              </Row>
            )
          })
        }
      </CheckboxGroup>
    )
  }

  render() {
    const {getFieldDecorator} = this.props.form
    return (
      <div id={'risk-box'}>
        <Form onSubmit={this.handleSubmit}>
          <h4>提醒内容</h4>
          <FormItem>
            {
              getFieldDecorator('content',{
                rules: [{ required: true, message: '请填写内容!' }]
              })(<Input.TextArea />)
            }
          </FormItem>

          <FormItem>
            <h4>选择你要提醒的对象</h4>
            <div className={'List-people'}>
              {
                getFieldDecorator('targets',{
                  rules: [{ required: true, message: '请选择要提醒的对象!' }]
                })(this.retureJsx())
              }
            </div>
          </FormItem>

          <FormItem>
            <Button htmlType='submit' type='primary'>提交</Button>
          </FormItem>

        </Form>
      </div>
    )
  }
}
export default Form.create()(Task);
